import * as React from 'react';
import { connect } from 'react-redux';
import { ApplicationState } from '../../../store';
import * as StationStore from '../../../store/Station';
import StationService from '../../../services/station.service';
import WorkbenchsView from './WorkbenchsView';

type WorkbenchsProps =
    StationStore.StationState &
    typeof StationStore.actionCreators;

interface IState {
    isLoading: boolean
}

class Workbenchs extends React.PureComponent<WorkbenchsProps, IState> {
    constructor(props: WorkbenchsProps) {
        super(props);
        this.state = {
            isLoading: true
        };

        this.searchStations(this.props);
    }

    searchStations(props: WorkbenchsProps) {
        props.deleteAllStationControllers();
        this.setState({
            isLoading: true
        })

        StationService
            .getStations()
            .then(scs => {
                scs.forEach((sc: StationStore.StationController) => {
                    props.createStationController(sc);

                    sc.stations.forEach((s: StationStore.Station) =>
                        props.createStation(s, sc.id)
                    )
                })

                this.setState({
                    isLoading: false
                })
            });
    }

    public render() {
        const handleSetTraceabilityModeClick = (scUrl: string, newState: boolean) => {
            StationService.setTraceabilityMode(scUrl, newState);
        }

        return (
            <WorkbenchsView
                stationControllers={this.props.stationControllers}
                isLoading={this.state.isLoading}
                handleSetTraceabilityModeClick={handleSetTraceabilityModeClick}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        stationControllers: state.station.stationControllers
    };
};

export default connect(
    mapStateToProps,
    StationStore.actionCreators
)(Workbenchs);
