import Configuration from '../configuration';
import FakeService from './fake.service';
import RestService from './restService';

class ProcessOptimizationService {
    static async getBetterOptimizationByCartridge(solderings) {
        let betterSoldering;

        if (Configuration.FakeMode) {
            betterSoldering = FakeService.getBetterOptimizationByCartridge();
        }
        else {
            betterSoldering = RestService.getBetterOptimizationByCartridge(solderings);
        }

        return betterSoldering;
    }

    static async getBetterOptimizationByTemperature(solderings) {
        let betterSoldering;

        if (Configuration.FakeMode) {
            betterSoldering = FakeService.getBetterOptimizationByTemperature();
        }
        else {
            betterSoldering = RestService.getBetterOptimizationByTemperature(solderings);
        }

        return betterSoldering;
    }
}

export default ProcessOptimizationService;
