import * as React from 'react';
import { Link } from 'react-router-dom';
import { Row, Col } from 'reactstrap';
import { Button, NavLink } from 'reactstrap';
import { Card, CardText, CardImg, CardBody, CardTitle } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'
import { ResponsiveContainer, PieChart, Pie, Cell } from 'recharts';
import * as TaskStore from '../../../store/Task';
import * as TaskHistoryStore from '../../../store/TaskHistory';
import { ToolToString, ToolImagePath } from '../../../utils/toolUtils';
import { GetPlotSeriesData } from '../../../utils/plotUtils';
import { ClassificationToString } from '../../../utils/solderingClassificationUtils';
import Plot2Axis from '../../content-parts/Plot2Axis';
import PlotPowerAxisSimple from '../../content-parts/PlotPowerAxisSimple';
import './TaskHistoryDetail.css';

type TaskHistoryDetailProps = {
    task: TaskStore.Task,
    taskHistory: TaskHistoryStore.TaskHistory
}

class TaskHistoryDetailView extends React.PureComponent<TaskHistoryDetailProps> {
    public render() {
        const getDetailsCard = () => {
            const COLORS = ['#00C49F', '#dc3545'];

            let workSolderings = this.props.taskHistory.workData.workSolderings;

            let correct = workSolderings.filter(x => x.solderingType === 0).length;
            let incorrect = workSolderings.length - correct;

            const data = [
                { name: 'Correct', value: correct },
                { name: 'Incorrect', value: incorrect }
            ];

            let stationDetail =
                <Row>
                    <Col xs="6">
                        <CardText>Finished date: {new Date(this.props.taskHistory.finishDate).toLocaleDateString('es-ES')}</CardText>
                        <CardText>Task name: {this.props.task.name}</CardText>
                        <CardText>Task code: {this.props.task.id}</CardText>
                        <CardText>Station Id: {this.props.taskHistory.workData.stationId}</CardText>
                        <CardText>Station name: {this.props.taskHistory.workData.stationName}</CardText>
                        <CardText>Port: {this.props.taskHistory.workData.stationPort + 1}</CardText>
                    </Col>
                    <Col xs="6">
                        <ResponsiveContainer>
                            <PieChart>
                                <Pie
                                    data={data}
                                    dataKey="value"
                                >
                                    {
                                        data.map((entry, index) => <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />)
                                    }
                                </Pie>
                            </PieChart>
                        </ResponsiveContainer>
                    </Col>
                </Row>

            let card =
                <Card>
                    <CardBody>
                        <CardTitle>Details</CardTitle>
                        {stationDetail}
                    </CardBody>
                </Card>

            return (
                <React.Fragment>
                    {card}
                </React.Fragment>
            )
        }

        const getConfigurationCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardTitle>Configuration</CardTitle>
                        <Row>
                            <Col xs="8">
                                <CardText>Tool: {ToolToString(this.props.taskHistory.workConfig.tool)}</CardText>
                                {this.props.taskHistory.workConfig.change_selected_temp ?
                                    <CardText>Selected temperature: {this.props.taskHistory.workConfig.selected_temp / 9} C </CardText>: ""}
                            </Col>
                            <Col xs="4">
                                <CardImg
                                    src={ToolImagePath(this.props.taskHistory.workConfig.tool)}
                                    alt={ToolToString(this.props.taskHistory.workConfig.tool)} />
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getRegisteredDataCard = () => {
            let plotData = GetPlotSeriesData(this.props.taskHistory.workData.temperature, this.props.taskHistory.workData.power);

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Registered data</CardTitle>
                        <CardText className="p-3">
                            <Plot2Axis
                                plotData={plotData}
                                counterTenthSecond={plotData.length}
                                interval={Math.floor(plotData.length / 5)}
                            />
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        const getSolderingsCard = () => {
            let workSolderings = this.props.taskHistory.workData.workSolderings;

            let registeredSolderings = workSolderings.map(x => {
                let circleLight = "circle-light-" + (x.solderingType === 0 ? "0" : "1")

                return (
                    <Col>
                        <Card className="pr-opti-plot">
                            <CardBody>
                                <CardText>
                                    Detected defect: <FontAwesomeIcon icon={faCircle} className={circleLight + " pl-1"}/>
                                    <strong className="pl-1">{ClassificationToString(x.solderingType)}</strong>
                                </CardText>
                                <PlotPowerAxisSimple
                                    plotData={x.power}
                                />
                            </CardBody>
                        </Card>
                    </Col>
                );
            });

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Detected solderings</CardTitle>
                        <Row xs={1} sm={2} md={3}>
                            {registeredSolderings}
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        return (
            <React.Fragment>
                <Row className="mb-1">
                    <h1>{this.props.task.name}</h1>
                </Row>
                <Row className="mb-3">
                    <Col xs="6" className="pl-0">
                        {getDetailsCard()}
                    </Col>
                    <Col xs="6" className="pl-0 pr-0">
                        {getConfigurationCard()}
                    </Col>
                </Row>
                <Row className="mb-3">
                    {getSolderingsCard()}
                </Row>
                <Row>
                    {getRegisteredDataCard()}
                </Row>
                <Row>
                    <Button color="danger" size="sm" className="m-2">
                        <NavLink
                            tag={Link}
                            className="text-white p-0"
                            to={"/traceability/task/" + this.props.task.id + "/detail"}>Return</NavLink>
                    </Button>
                </Row>
            </React.Fragment>
        );
    }
};

export default TaskHistoryDetailView;
