import * as React from 'react';
import { connect } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Button } from 'reactstrap';
import { Alert } from 'reactstrap';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { Row, Col } from 'reactstrap';
import { Card, CardText, CardImg, CardBody, CardTitle } from 'reactstrap';
import { ApplicationState } from '../../../store';
import * as TaskStore from '../../../store/Task';
import * as StationStore from '../../../store/Station';
import TasksService from '../../../services/tasks.service';
import WorkService from '../../../services/work.service';
import { IRealTimeData } from './data/IRealTimeData';
import { ToolToString, ToolImagePath } from '../../../utils/toolUtils';
import { EmptyPlotSeriesData, PushPlotSeriesData } from '../../../utils/plotUtils';
import StationListSide from '../../content-parts/StationListSide';
import WorkProcessStep1 from './WorkProcessStep1';
import WorkProcessStep2 from './WorkProcessStep2';
import WorkProcessStep3 from './WorkProcessStep3';

const WAIT_INTERVAL = 1000;

type WorkProcessProps =
    TaskStore.TaskState &
    StationStore.StationState &
    typeof TaskStore.actionCreators &
    RouteComponentProps<{}>;

interface IState {
    selectedTask: TaskStore.Task,
    selectedStation: StationStore.Station,
    selectedPort: number,

    queueId: number,
    plotData: IRealTimeData[],
    counterTenthSecond: number,
    readyGetMoreData: boolean,

    currentStep: number,
    showFinishTaskModal: boolean,
    timer: number,

    nTotalTasks: number,
    pageNumber: number,
    pageSize: number,
    filter: string,

    isLoadingTasks: boolean
}

const MAX_POINTS_PLOT = 400;
const INTERVAL_REFRESH_PLOT_MILLISCONDS = 100;

class WorkProcess extends React.PureComponent<WorkProcessProps, IState> {
    constructor(props: WorkProcessProps) {
        super(props);

        let plotData = EmptyPlotSeriesData(MAX_POINTS_PLOT) as IRealTimeData[];

        this.state = {
            selectedTask: {
                id: 0,
                name: "",
                workConfig: {
                    tool: 0,
                    selectedTemp: 0,
                    changeSelectedTemp: false
                },
                timesPerformed: 0
            },
            selectedStation: {
                id: "",
                name: "",
                model: "",
                isQSTMode: false,
                ports: []
            },
            selectedPort: -1,

            queueId: -1,
            plotData: plotData,
            counterTenthSecond: 0,
            readyGetMoreData: true,

            currentStep: 1,
            showFinishTaskModal: false,
            timer: -1,

            nTotalTasks: 0,
            pageNumber: 1,
            pageSize: 25,
            filter: "",

            isLoadingTasks: true
        };

        this.searchTasks(this.props, this.state.filter, this.state.pageNumber);

        setInterval(() => {
            if (this.state.queueId > -1 &&
                this.state.readyGetMoreData) {

                this.setState({
                    readyGetMoreData: false
                })

                WorkService
                    .getRealTimeData(this.state.selectedStation.id, this.state.queueId)
                    .then((values: IRealTimeData[]) => {
                        let plotData = this.state.plotData;
                        let counterTenthSecond = this.state.counterTenthSecond

                        values.forEach(x => {
                            PushPlotSeriesData(plotData, x, MAX_POINTS_PLOT);
                            counterTenthSecond++
                        });

                        this.setState({
                            plotData: plotData,
                            readyGetMoreData: true,
                            counterTenthSecond: counterTenthSecond
                        })
                    })
            }
        }, INTERVAL_REFRESH_PLOT_MILLISCONDS);
    }

    searchTasks(props: WorkProcessProps, filter: string, pageNumber: number) {
        props.deleteAllTasks();

        this.setState({
            isLoadingTasks: true,
            pageNumber: pageNumber,
            filter: filter
        })

        TasksService
            .getTasks(filter, pageNumber, this.state.pageSize)
            .then(tasks => {
                tasks.forEach((x: TaskStore.Task) =>
                    props.createTask(x)
                )

                this.setState({
                    isLoadingTasks: false
                })
            });

        TasksService
            .countTasks(filter)
            .then(value => {
                this.setState({
                    nTotalTasks: value
                })
            });
    }

    public render() {
        const getMetadata = () => {
            if (this.state.currentStep == 1) {
                return (
                    <Alert color="danger">
                        Select a task to continue.
                    </Alert>
                )
            }

            let alert;
            if (this.state.selectedStation.id == "") {
                alert =
                    <Alert color="danger" className="mt-3">
                        Select a station to continue.
                    </Alert>
            }

            return (
                <React.Fragment>
                    {getDetails()}
                    {getConfiguration()}
                    {alert}
                </React.Fragment>
            )
        }

        const getDetails = () => {
            let taskDetail =
                <React.Fragment>
                    <CardText>Task name: {this.state.selectedTask.name}</CardText>
                    <CardText>Task code: {this.state.selectedTask.id}</CardText>
                </React.Fragment>

            let stationDetail;
            if (this.state.selectedStation.id != "") {
                stationDetail =
                    <React.Fragment>
                        <CardText>Station Id: {this.state.selectedStation.id}</CardText>
                        <CardText>Station: {this.state.selectedStation.name} [{this.state.selectedStation.model}]</CardText>
                        <CardText>Port: {this.state.selectedPort + 1}</CardText>
                    </React.Fragment>
            }

            let card =
                <Card>
                    <CardBody>
                        <CardTitle>Details</CardTitle>
                        <Row className="pd-1">
                            <Col xs="6">{taskDetail}</Col>
                            <Col xs="6">{stationDetail}</Col>
                        </Row>
                    </CardBody>
                </Card>

            return (
                <Row className="mb-2">
                    {card}
                </Row>
            )
        };

        const getConfiguration = () => {
            return (
                <Row className="mb-2">
                    <Card>
                        <CardBody>
                            <CardTitle>Configuration</CardTitle>
                            <Row>
                                <Col xs="8">
                                    <CardText>Tool: {ToolToString(this.state.selectedTask.workConfig.tool)}</CardText>
                                    {this.state.selectedTask.workConfig.changeSelectedTemp ?
                                        <CardText>Selected temperature: {this.state.selectedTask.workConfig.selectedTemp / 9} C </CardText>: ""}
                                </Col>
                                <Col xs="4">
                                    <CardImg
                                        src={ToolImagePath(this.state.selectedTask.workConfig.tool)}
                                        alt={ToolToString(this.state.selectedTask.workConfig.tool)} />
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Row>
            )
        }

        const getPreviousButton = () => {
            if (this.state.currentStep == 2) {
                return (
                    <Button
                        outline
                        color="danger"
                        size="sm"
                        className="m-2"
                        onClick={() => previous()}
                    >Previous</Button>
                )
            }
        }

        const getNextButton = () => {
            let text = "";
            let isValid = false;

            if (this.state.currentStep == 2) {
                text = "Start task";
                isValid = this.state.selectedStation.id != "";
            }
            else if (this.state.currentStep == 3) {
                text = "Stop task";
                isValid = true;
            }

            if (!isValid) {
                return "";
            }

            return (
                <Button
                    color="danger"
                    size="sm"
                    className="m-2"
                    onClick={() => next()}
                >{text}</Button>
            )
        }

        const previous = () => {
            this.setState({
                currentStep: this.state.currentStep - 1
            })
        }

        const next = () => {
            if (this.state.currentStep == 1) {
                this.setState({
                    currentStep: 2
                })
            }
            else if (this.state.currentStep == 2) {
                let plotData = EmptyPlotSeriesData(MAX_POINTS_PLOT) as IRealTimeData[];

                this.setState({
                    plotData: plotData,
                    counterTenthSecond: 0
                })

                WorkService
                    .startTrace(this.state.selectedTask.id, this.state.selectedStation.id, this.state.selectedPort)
                    .then(() =>
                        this.setState({
                            currentStep: 3
                        })
                );

                WorkService
                    .startRealTimeData(this.state.selectedStation.id, this.state.selectedPort)
                    .then((value) => {
                        this.setState({
                            queueId: value
                        })
                    })
            }
            else {
                this.setState({
                    showFinishTaskModal: true
                })
            }
        }

        const handleTaskClick = (task: TaskStore.Task) => {
            this.setState({
                selectedTask: task,
                currentStep: 2
            })
        }

        const handleStationClick = (station: StationStore.Station, port: number) => {
            this.setState({
                selectedStation: station,
                selectedPort: port
            })
        }

        const handleFinishTaskConfirmClick = () => {
            WorkService
                .stopTrace(this.state.selectedStation.id, this.state.selectedPort);

            var queueId = this.state.queueId;

            this.setState({
                queueId: -1,
                currentStep: 2,
                showFinishTaskModal: false
            });

            WorkService
                .stopRealTimeData(this.state.selectedStation.id, queueId);
        };

        const handleFinishTaskCancelClick = () => {
            this.setState({
                showFinishTaskModal: false
            });
        };

        const handlePageClick = (pageNumber: number) => {
            this.searchTasks(
                this.props,
                this.state.filter,
                pageNumber);
        }

        const handlePreviousPageClick = () => {
            this.searchTasks(
                this.props,
                this.state.filter,
                Math.max(1, this.state.pageNumber - 1));
        }

        const handleNextPageClick = () => {
            this.searchTasks(
                this.props,
                this.state.filter,
                Math.min(
                    Math.ceil(this.state.nTotalTasks / this.state.pageSize),
                    this.state.pageNumber + 1));
        }

        const handleSearchFieldChange = (filter: string) => {
            if (this.state.timer > 0)
                window.clearTimeout(this.state.timer);

            this.setState({
                pageNumber: 1,
                timer: setTimeout(this.searchTasks, WAIT_INTERVAL, this.props, filter, 1)
            });
        }

        return (
            <React.Fragment>
                <Row className="mb-2">
                    <h1>Work process</h1>
                </Row>
                <Row>
                    <Col xs="9">
                        <WorkProcessStep3
                            taskId={this.state.selectedTask.id}
                            stationId={this.state.selectedStation.id}
                            queueId={this.state.queueId}
                            currentStep={this.state.currentStep}
                            plotData={this.state.plotData}
                            counterTenthSecond={this.state.counterTenthSecond}
                            interval={MAX_POINTS_PLOT / 5}
                        />
                        {getMetadata()}
                        <WorkProcessStep1
                            currentStep={this.state.currentStep}
                            tasks={this.props.tasks}
                            nTotalTasks={this.state.nTotalTasks}
                            pageSize={this.state.pageSize}
                            pageNumber={this.state.pageNumber}
                            isLoadingTasks={this.state.isLoadingTasks}

                            handlePageClick={handlePageClick}
                            handlePreviousPageClick={handlePreviousPageClick}
                            handleNextPageClick={handleNextPageClick}
                            handleTaskClick={handleTaskClick}
                            handleSearchFieldChange={handleSearchFieldChange}
                        />
                        <WorkProcessStep2
                            stationControllers={this.props.stationControllers}
                            taskTool={this.state.selectedTask.workConfig.tool}
                            currentStep={this.state.currentStep}
                            handleStationClick={handleStationClick}
                        />
                        <Row>
                            {getPreviousButton()}
                            {getNextButton()}
                        </Row>
                    </Col>
                    <Col xs="3">
                        <StationListSide/>
                    </Col>
                </Row>

                <Modal isOpen={this.state.showFinishTaskModal} toggle={() => handleFinishTaskCancelClick()}>
                    <ModalHeader toggle={() => handleFinishTaskCancelClick()}>Finish task</ModalHeader>
                    <ModalBody>Are you sure to finish the task?</ModalBody>
                    <ModalFooter>
                        <Button outline color="danger" onClick={() => handleFinishTaskConfirmClick()}>Confirm</Button>{' '}
                        <Button color="danger" onClick={() => handleFinishTaskCancelClick()}>Cancel</Button>
                    </ModalFooter>
                </Modal>
            </React.Fragment>
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        stationControllers: state.station.stationControllers,
        tasks: state.task.tasks
    };
};

export default connect(
    mapStateToProps,
    TaskStore.actionCreators
)(WorkProcess);
