import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Button, Input } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import * as StationStore from '../../../store/Station';
import { LoadCartridges, ICartridge } from '../../../utils/cartridgeUtils';
import StationsPortList from '../../content-parts/StationsPortList';

type SolderingAssistantProps = {
    currentStep: number,
    isLoading: boolean,
    isFormCorrect: boolean,

    stationControllers: StationStore.StationController[],

    selectedTool: number,
    selectedTemperature: number,

    handleStationClick: (station: StationStore.Station, port: number, tool: number) => void,
    handleSelecTempChange: (temperature: string) => void,
    handleCartridgeChange: (value: string) => void,
    handleNameChange: (value: string) => void,
    handleNextClick: () => void
}

interface IState {
    cartridges: ICartridge[],
    cartridgeImagePath: string
}

class SolderingAssistantNewStep1 extends React.PureComponent<SolderingAssistantProps, IState> {
    constructor(props: SolderingAssistantProps) {
        super(props);
        
        let cartridgeImagePath = "";
        let cartridges = LoadCartridges();

        this.state = {
            cartridges: cartridges,
            cartridgeImagePath: cartridgeImagePath
        };
    }

    public render() {
        if (this.props.currentStep !== 1) {
            return null
        }
        
        const getListOptionsStationControllers = () => {
            let filtered = this.props.stationControllers.map(sc => {
                let stations = sc.stations.map(s => {
                    let ports = s.ports.filter(p => p.tool === 1 || p.tool === 2);
                    s.ports = ports;
                    return s;
                });
                sc.stations = stations;
                return sc;
            });

            return (
                <StationsPortList
                    isLoading={this.props.isLoading}
                    stationControllers={filtered}
                    handleStationClick={handleStationClick}
                />
            );
        };

        const handleStationClick = (station: StationStore.Station, port: number, tool: number) => {
            this.props.handleStationClick(station, port, tool);

            let cartridgeImagePath = "";
            let defaultCartridge = this.state.cartridges.find(x => x.isDefault && x.tool === tool);
        
            if (defaultCartridge !== undefined) {
                cartridgeImagePath = defaultCartridge.img;
                this.props.handleCartridgeChange(defaultCartridge.fullName);
            }
    
            this.setState ({
                cartridgeImagePath: cartridgeImagePath
            });
        }

        const getCartridges = () => {
            let cartridges = this.state.cartridges
                .filter(x => x.tool === this.props.selectedTool)
                .map(x => (
                    <option selected={x.isDefault}>{x.fullName}</option>
                ));

            return cartridges;
        }

        const handleCartridgeChange = (value: string) => {
            this.props.handleCartridgeChange(value);
            let cartridge = this.state.cartridges.find(x => x.fullName === value);
            let cartridgeImagePath = cartridge === undefined ? "" : cartridge.img;

            this.setState({
                cartridgeImagePath: cartridgeImagePath
            });
        }

        const getConfigurationCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardTitle>Configuration</CardTitle>
                        <Row>
                            <Col xs="6">
                                <CardText>
                                    <strong>Selected temperature:</strong>
                                    <Input
                                        defaultValue={this.props.selectedTemperature / 9}
                                        onChange={(e) => this.props.handleSelecTempChange(e.target.value)}
                                    />
                                </CardText>
                                <CardText>
                                    <strong>Selected cartridge:</strong>
                                    <Input
                                        type="select"
                                        onChange={(e) => handleCartridgeChange(e.target.value)}
                                    >
                                        {getCartridges()}
                                    </Input>
                                </CardText>
                                <CardText>
                                    <strong>Name:</strong>
                                    <Input
                                        onChange={(e) => this.props.handleNameChange(e.target.value)}
                                    />
                                </CardText>
                            </Col>
                            <Col xs="6">
                                <img src={this.state.cartridgeImagePath} />
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getNextButton = () => {
            if (!this.props.isFormCorrect) {
                return "";
            }

            return (
                <Button
                    outline
                    color="danger"
                    size="sm"
                    className="m-2"
                    onClick={this.props.handleNextClick}
                >Continue</Button>)
        }

        return (
            <React.Fragment>
                <Row className="mb-1">
                    <h1>New soldering reference</h1>
                </Row>
                <Row>
                    <Col xs="8" className="pr-0">
                        <Card>
                            <CardBody>
                                <CardTitle>Stations available</CardTitle>
                                <CardText>
                                    {getListOptionsStationControllers()}
                                </CardText>
                            </CardBody>
                        </Card>
                    </Col>
                    <Col xs="4" className="pr-0">
                        {getConfigurationCard()}
                    </Col>
                </Row>
                <Row>
                    {getNextButton()}
                </Row>
            </React.Fragment>
        );
    }
};

export default SolderingAssistantNewStep1;
