import * as React from 'react';
import * as StationStore from '../../../store/Station';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingData } from './data/ISolderingData';
import { IOptimizedSoldering } from './data/IOptimizedSoldering';
import ProcessOptimizationStep1 from './ProcessOptimizationStep1';
import ProcessOptimizationStep2 from './ProcessOptimizationStep2';
import ProcessOptimizationStep3 from './ProcessOptimizationStep3';
import ProcessOptimizationStep4 from './ProcessOptimizationStep4';
import ProcessOptimizationStep5 from './ProcessOptimizationStep5';

type ProcessOptimizationProps = {
    currentStep: number,
    isLoading: boolean,
    isFormCorrect: boolean,

    stationControllers: StationStore.StationController[],
    handleStationClick: (station: StationStore.Station, port: number, tool: number) => void,
    handleSelecTempChange: (temperature: string) => void,
    handleCartridgeChange: (value: string) => void,
    handleDeleteRealTimeData: (value: string) => void,
    handleNextClick: () => void,

    selectedStation: StationStore.Station,
    selectedPort: number,
    selectedTool: number,
    selectedTemperature: number,
    selectedCartridge: string,

    lastRealTimeData: IRealTimeData,
    counterTenthSecond: number,

    detectedSolderings: ISolderingData[],
    fasterSoldering: ISolderingData,
    optimizedSoldering: IOptimizedSoldering
}

class ProcessOptimizationView extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        return (
            <React.Fragment>
                <ProcessOptimizationStep1
                    currentStep={this.props.currentStep}
                    handleNextClick={this.props.handleNextClick}
                />
                <ProcessOptimizationStep2
                    currentStep={this.props.currentStep}
                    isLoading={this.props.isLoading}
                    isFormCorrect={this.props.isFormCorrect}
                    
                    stationControllers={this.props.stationControllers}
                    selectedTemperature={this.props.selectedTemperature}
                    
                    handleStationClick={this.props.handleStationClick}
                    handleSelecTempChange={this.props.handleSelecTempChange}
                    handleNextClick={this.props.handleNextClick}
                />
                <ProcessOptimizationStep3
                    currentStep={this.props.currentStep}
                    
                    selectedStation={this.props.selectedStation}
                    selectedPort={this.props.selectedPort}
                    selectedTool={this.props.selectedTool}
                    selectedTemperature={this.props.selectedTemperature}

                    lastRealTimeData={this.props.lastRealTimeData}
                    counterTenthSecond={this.props.counterTenthSecond}

                    detectedSolderings={this.props.detectedSolderings}

                    handleCartridgeChange={this.props.handleCartridgeChange}
                    handleDeleteRealTimeData={this.props.handleDeleteRealTimeData}
                    handleNextClick={this.props.handleNextClick}
                />
                <ProcessOptimizationStep4
                    currentStep={this.props.currentStep}
                    isLoading={this.props.isLoading}

                    selectedStation={this.props.selectedStation}
                    selectedPort={this.props.selectedPort}
                    selectedTemperature={this.props.selectedTemperature}

                    lastRealTimeData={this.props.lastRealTimeData}
                    counterTenthSecond={this.props.counterTenthSecond}

                    detectedSolderings={this.props.detectedSolderings}
                    fasterSoldering={this.props.fasterSoldering}

                    handleSelecTempChange={this.props.handleSelecTempChange}
                    handleDeleteRealTimeData={this.props.handleDeleteRealTimeData}
                    handleNextClick={this.props.handleNextClick}
                />
                <ProcessOptimizationStep5
                    currentStep={this.props.currentStep}
                    isLoading={this.props.isLoading}

                    optimizedSoldering={this.props.optimizedSoldering}
                    selectedCartridge={this.props.selectedCartridge}

                    handleNextClick={this.props.handleNextClick}
                />
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationView;
