import * as AssistantStore from '../store/Assistant';
import * as StationStore from '../store/Station';
import * as TaskStore from '../store/Task';
import { sleep } from '../utils/timeUtils';

class FakeService {
    static components = [
        "surface", "mount", "resistor", "led",
        "R1", "R2", "R10", "R20", "PCB",
        "C1", "C2", "C10", "C20",
        "diode", "transistor", "capacitor", "magnetic",
        "switches", "terminals", "thru-hole"]

    // assistant

    static async getSolderingResult() {
        console.log("FAKE SERVICE: getSolderingResult");
        return {
            energy: 46,
            similarity: 800,
            tenthsOfSecondSoldering: 39
        };
    }

    static async countReferences() {
        console.log("FAKE SERVICE: countReferences");
        return 60;
    }

    static async getReferences() {
        console.log("FAKE SERVICE: getReferences");
        await sleep(1000);
        return this._getReferences(15);
    }

    static async _getReferences(nReferences: number) {
        let references = [] as AssistantStore.Reference[];

        for (var id = 1; id <= nReferences; id++) {
            let referenceName = "";
            for (var i = 0; i < 4; i++) {
                referenceName += this.components[Math.floor(Math.random() * this.components.length)] + " ";
            }

            let reference = {
                id: id,
                name: referenceName,
                power: [109, 136, 162, 181, 205, 216, 208, 199, 182, 165, 151, 141, 136, 132,
                        131, 131, 134, 141, 154, 165, 178, 185, 198, 207, 210, 207, 203, 194,
                        186, 178, 158, 144, 127],
                cartridge: 'C245004',
                temperature: 350 * 9,
                durationTenthsSeconds: 64
            };

            references.push(reference);
        }

        return references;
    }

    static async deleteReference() {
        console.log("FAKE SERVICE: deleteReference");
        await sleep(1000);
    }

    static async generateReferenceSoldering() {
        console.log("FAKE SERVICE: generateReferenceSoldering");
        await sleep(1000);
    }

    // tasks

    static async countTasks() {
        console.log("FAKE SERVICE: countTasks");
        return 60;
    }

    static async getTasks() {
        console.log("FAKE SERVICE: getTasks");
        await sleep(1000);
        return this._getTasks(25);
    }

    static async getTaskById() {
        console.log("FAKE SERVICE: getTaskById");
        await sleep(1000);
        return (await this._getTasks(1))[0];
    }

    static async _getTasks(nTasks: number) {
        let tasks = [] as TaskStore.Task[];

        for (var taskId = 1; taskId <= nTasks; taskId++) {

            let taskName = "";
            for (var i = 0; i < 4; i++) {
                taskName += this.components[Math.floor(Math.random() * this.components.length)] + " ";
            }

            let tool = Math.floor(Math.random() * 8) + 1;
            let selectedTemp = (Math.floor(Math.random() * 100) + 300) * 9;
            let change_selected_temp = true;

            let task = {
                id: taskId,
                name: taskName,
                workConfig: {
                    tool: tool,
                    selectedTemp: selectedTemp,
                    changeSelectedTemp: change_selected_temp
                },
                timesPerformed: Math.floor(Math.random() * 3)
            };

            tasks.push(task);
        }

        return tasks;
    }

    static async createTask(task: TaskStore.Task) {
        console.log("FAKE SERVICE: createTask");
        await sleep(1000);
    }

    static async updateTask(task: TaskStore.Task) {
        console.log("FAKE SERVICE: updateTask");
        await sleep(1000);
    }

    static async deleteTask(id: number) {
        console.log("FAKE SERVICE: deleteTask");
        await sleep(1000);
    }

    // task histories

    static async countTaskHistories(taskId: number) {
        console.log("FAKE SERVICE: countTaskHistories");
        return 60;
    }

    static async getTaskHistories(taskId: number) {
        console.log("FAKE SERVICE: getTaskHistories");
        await sleep(2000);
        return this._getTaskHistories(25)
    }

    static async getTaskHistoryById() {
        console.log("FAKE SERVICE: getTaskHistoryById");
        await sleep(1000);
        return (await this._getTaskHistories(1))[0];
    }

    static async _getTaskHistories(nTasks: number) {
        let taskHistories = []

        for (var historyId = 1; historyId <= nTasks; historyId++) {
            let length = Math.floor(Math.random() * 500) + 30;

            let tool = Math.floor(Math.random() * 8) + 1;
            let selectedTemp = (Math.floor(Math.random() * 100) + 300) * 9;
            let change_selected_temp = true;

            let taskHistory = {
                id: historyId + 1000,
                finishDate: Date.now(),
                workConfig: {
                    tool: tool,
                    selected_temp: selectedTemp,
                    change_selected_temp: change_selected_temp
                },
                workData: {
                    stationId: "U1H23UI12HN3IBN",
                    stationName: "Roberto's DDE",
                    stationPort: 1,
                    temperature: Array.from({ length: length }, () => ((Math.floor(Math.random() * 100)) + 300) * 9),
                    power: Array.from({ length: length }, () => Math.floor(Math.random() * 200)),
                    workSolderings: [
                        {
                            power: Array.from({ length: 35 }, () => Math.floor(Math.random() * 200)),
                            solderingType: 0
                        },
                        {
                            power: Array.from({ length: 35 }, () => Math.floor(Math.random() * 200)),
                            solderingType: Math.floor(Math.random() * 6)
                        },
                        {
                            power: Array.from({ length: 35 }, () => Math.floor(Math.random() * 200)),
                            solderingType: Math.floor(Math.random() * 6)
                        }
                    ]
                }
            };

            taskHistories.push(taskHistory);
        }

        return taskHistories;
    }

    // work

    static async startTrace() {
        console.log("FAKE SERVICE: startTrace");
        await sleep(1000);
    }

    static async stopTrace() {
        console.log("FAKE SERVICE: stopTrace");
        await sleep(1000);
    }

    static async startRealTimeData() {
        console.log("FAKE SERVICE: startRealTimeData");
        await sleep(1000);
        return 1;
    }

    static async stopRealTimeData() {
        console.log("FAKE SERVICE: stopRealTimeData");
        await sleep(1000);
    }

    static async getRealTimeData() {
        console.log("FAKE SERVICE: getRealTimeData");

        var d = new Date();
        var seconds = d.getSeconds() % 10;

        var isPortEnabled = seconds > 0;

        var toolStatus = 0;
        if (seconds === 1) toolStatus = 8;
        if (seconds === 2) toolStatus = 4;
        if (seconds === 3) toolStatus = 2;
        if (seconds === 4) toolStatus = 1;

        var isCalibrating =
            (seconds >= 5) &&
            (seconds <= 6);
        
        var isSoldering =
            (seconds >= 8) &&
            (seconds <= 9);

        return [{
            temperature: (Math.floor(Math.random() * 100) + 300) * 9,
            power: Math.floor(Math.random() * 100) + 100,
            toolStatus: toolStatus,
            isPortEnabled: isPortEnabled,
            isSoldering: isSoldering,
            isCalibrating: isCalibrating
        },
        {
            temperature: (Math.floor(Math.random() * 100) + 300) * 9,
            power: Math.floor(Math.random() * 100) + 100,
            toolStatus: toolStatus,
            isPortEnabled: isPortEnabled,
            isSoldering: isSoldering,
            isCalibrating: isCalibrating
        }]
    }

    // process optimization

    static async getBetterOptimizationByCartridge() {
        console.log("FAKE SERVICE: getBetterOptimizationByCartridge");
        await sleep(2000);

        let data = [];
        for (var i = 0; i < 35; i++) {
            data.push({
                temperature: (Math.floor(Math.random() * 100) + 300) * 9,
                power: (Math.floor(Math.random() * 100) + 100) * 10
            })
        }

        return {
            data: data,
            energy: data.reduce(((total, x) => total + x.power), 0),
            time: data.length,
            cartridge: "C210004",
            selectedTemp: 350 * 9
        }
    }

    static async getBetterOptimizationByTemperature() {
        console.log("FAKE SERVICE: getBetterOptimizationByTemperature");
        await sleep(2000);

        let points = [{
            temperature: 250 * 9,
            time: 3
        }, {
            temperature: 300 * 9,
            time: 1.75
        }, {
            temperature: 350 * 9,
            time: 1
        }, {
            temperature: 400 * 9,
            time: 0.5
        }];

        let recommended = {
            temperature: 350 * 9,
            time: 1
        };

        let performance = {
            temperature: 400 * 9,
            time: 0.5
        };

        let durability = {
            temperature: 300 * 9,
            time: 1.75
        };

        return {
            points: points,
            recommended: recommended,
            performance: performance,
            durability: durability
        }
    }

    // stations

    static async getStations() {
        console.log("FAKE SERVICE: getStations");
        await sleep(2000);
        let scs = [] as StationStore.StationController[]

        for (var iSc = 1; iSc <= 2; iSc++) {
            let scId = "sc" + iSc
            let sc =
                {
                    id: scId,
                    url: "192.168.1.1",
                    name: "Lab-" + iSc,
                    isQSTMode: Math.round(Math.random() * 1) === 1 ? true : false,
                    stations: []
                } as StationStore.StationController;

            for (var iSt = 1; iSt <= 2; iSt++) {
                let stationId = scId + "_" + iSt;
                let station =
                    {
                        id: stationId,
                        name: "Roberto's DDE " + iSt,
                        model: "DDE",
                        isQSTMode: Math.round(Math.random() * 1) === 1 ? true : false,
                        ports: []
                    } as StationStore.Station;

                let nPorts = Math.pow(2, Math.floor(Math.random() * 3));
                for (var p = 0; p < nPorts; p++) {
                    let port =
                        {
                            id: p,
                            tool: Math.floor(Math.random() * 8) + 1
                        } as StationStore.Port;
                    station.ports.push(port);
                }

                sc.stations.push(station);
            }

            scs.push(sc);
        }

        return scs;
    }

    static async setSelectedTemperature() {
        console.log("FAKE SERVICE: setSelectedTemperature");
    }

    static async setTraceabilityMode() {
        console.log("FAKE SERVICE: setTraceabilityMode");
    }
}

export default FakeService;
