import Configuration from '../configuration';
import FakeService from './fake.service';
import RestService from './restService';

class WorkService {
    static async startTrace(taskId, stationId, port) {
        if (Configuration.FakeMode) {
            FakeService.startTrace();
        }
        else {
            RestService.startTrace(taskId, stationId, port);
        }
    }

    static async stopTrace(stationId, port) {
        if (Configuration.FakeMode) {
            FakeService.stopTrace();
        }
        else {
            RestService.stopTrace(stationId, port);
        }
    }

    static async startRealTimeData(stationId, port) {
        let queueId;

        if (Configuration.FakeMode) {
            queueId = FakeService.startRealTimeData();
        }
        else {
            queueId = RestService.startRealTimeData(stationId, port);
        }

        return queueId;
    }

    static async stopRealTimeData(stationId, queueId) {
        if (Configuration.FakeMode) {
            FakeService.stopRealTimeData();
        }
        else {
            RestService.stopRealTimeData(stationId, queueId);
        }
    }

    static async getRealTimeData(stationId, queueId) {
        let data;

        if (Configuration.FakeMode) {
            data = FakeService.getRealTimeData();
        }
        else {
            data = RestService.getRealTimeData(stationId, queueId);
        }

        return data;
    }
}

export default WorkService;
