import Configuration from '../configuration';
import FakeService from './fake.service';
import RestService from './restService';

class TaskHistoriessService {
    static async countTaskHistories(taskId) {
        let count;

        if (Configuration.FakeMode) {
            count = FakeService.countTaskHistories();
        }
        else {
            count = RestService.countTaskHistories(taskId);
        }

        return count;
    }

    static async getTaskHistories(id, pageNumber, pageSize) {
        let tasks;

        if (Configuration.FakeMode) {
            tasks = FakeService.getTaskHistories(id);

        }
        else {
            tasks = RestService.getTaskHistories(id, pageNumber, pageSize);
        }

        return tasks;
    }

    static async getTaskHistoryById(id) {
        let task;

        if (Configuration.FakeMode) {
            task = FakeService.getTaskHistoryById(id);

        }
        else {
            task = RestService.getTaskHistoryById(id);
        }

        return task;
    }
}

export default TaskHistoriessService;
