export function GetClassifications()
{
    var values =
    {
        0: "Correct",
        1: "Low thermal contact",
        2: "Slow soldering",
        3: "Fact soldering",
        4: "Oscillations",
        5: "Excess tin",
        6: "Little tin"
    }
    return values;
}

export function ClassificationToString(id)
{
    let value = GetClassifications()[id];

    if (typeof(value) === "undefined") {
        return "Undefined";
    }
    else {
        return value;
    }
}
