export function GetTools()
{
    var tools =
    {
        1: "T210",
        2: "T245",
        3: "PA",
        4: "HT",
        5: "DS",
        6: "DR",
        7: "NT105",
        8: "NP105",
        9: "T470",

        31: "JT",
        32: "TE",
        33: "PHS",
        34: "PHB"
    }
    return tools;
}

export function ToolToString(id)
{
    let tool = GetTools()[id];

    if (typeof(tool) === "undefined") {
        return "No tool";
    }
    else {
        return tool;
    }
}

export function ToolImagePath(id)
{
    if (typeof(GetTools()[id]) === "undefined") {
        return "";
    }
    else {
        return "img/tools/" + GetTools()[id].toLowerCase() + ".png";
    }
}
