import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Card, CardBody } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'
import { GetPortStatus2String, GetPortStatus2Lights } from '../../../utils/portUtils';
import './PortStatusCard.css';

type PortStatusCardProps = {
    portData: IRealTimeData
}

interface IRealTimeData {
    temperature: number,
    power: number,
    toolStatus: number,
    isPortEnabled: boolean,
    isCalibrating: boolean,
    isSoldering: boolean,
    tenthsOfSecondSoldering: number
}

class PortStatusCard extends React.PureComponent<PortStatusCardProps> {
    public render() {
        let circleLight = "circle-light-" + GetPortStatus2Lights(
            this.props.portData.toolStatus,
            this.props.portData.isCalibrating,
            this.props.portData.isSoldering,
            this.props.portData.isPortEnabled);
            
        return (
            <Card>
                <CardBody>
                    <Row>
                        <Col xs="auto">
                            Temperature:{' '}
                            <span className="temp-value"> {Math.floor(this.props.portData.temperature / 9)} C</span>
                        </Col>
                        <Col>
                            Power:{' '}
                            <span className="power-value">{Math.floor(this.props.portData.power / 10)} %</span>
                        </Col>
                        <Col className="text-right">
                            <FontAwesomeIcon icon={faCircle} className={circleLight}/>
                            <strong className="pl-1">{GetPortStatus2String(
                                this.props.portData.toolStatus,
                                this.props.portData.isCalibrating,
                                this.props.portData.isSoldering,
                                this.props.portData.isPortEnabled
                            )}</strong>
                        </Col>
                    </Row>
                </CardBody>
            </Card>
        );
    }
};

export default PortStatusCard;
