import { Action, Reducer } from 'redux';

export interface Port
{
    id: number,
    tool: number
}

export interface Station
{
    id: string,
    name: string,
    model: string,
    isQSTMode: boolean,
    ports: Port[]
}

export interface StationController
{
    id: string,
    url: string,
    name: string,
    isQSTMode: boolean,
    stations: Station[]
}

export interface StationState {
    stationControllers: StationController[]
}

export interface CreateStationControllerAction { type: 'CREATE_STATION_CONTROLLER', sc: StationController }
export interface DeleteStationControllerAction { type: 'DELETE_STATION_CONTROLLER', id: string }
export interface DeleteAllStationControllersAction { type: 'DELETE_ALL_STATION_CONTROLLERS' }
export interface CreateStationAction { type: 'CREATE_STATION', station: Station, scId: string }
export interface DeleteStationAction { type: 'DELETE_STATION', id: string }

export type KnownAction =
    | CreateStationControllerAction
    | DeleteStationControllerAction
    | DeleteAllStationControllersAction
    | CreateStationAction
    | DeleteStationAction;

export const actionCreators = {
    createStationController: (sc: StationController) => ({ type: 'CREATE_STATION_CONTROLLER', sc: sc } as CreateStationControllerAction),
    deleteStationController: (id: string) => ({ type: 'DELETE_STATION_CONTROLLER', id: id } as DeleteStationControllerAction),
    deleteAllStationControllers: () => ({ type: 'DELETE_ALL_STATION_CONTROLLERS' } as DeleteAllStationControllersAction),
    createStation: (station: Station, scId: string) => ({ type: 'CREATE_STATION', station: station, scId: scId } as CreateStationAction),
    deleteStation: (id: string) => ({ type: 'DELETE_STATION', id: id } as DeleteStationAction)
};


export const reducer: Reducer<StationState> = (
    state: StationState | undefined,
    incomingAction: Action
): StationState => {
    if (state === undefined) {
        return { stationControllers: [] };
    }

    const action = incomingAction as KnownAction;
    switch (action.type) {
        case 'CREATE_STATION_CONTROLLER':
            if (state.stationControllers.find(x => x.id === action.sc.id)) {
                return {
                    ...state
                }
            }

            return {
                ...state,
                stationControllers: state.stationControllers.concat(action.sc)
            };

        case 'DELETE_STATION_CONTROLLER':
            return {
                ...state,
                stationControllers: state.stationControllers
                    .filter(x => {
                        if (x.id !== action.id) {
                            return x;
                        }
                    })
            };

        case 'DELETE_ALL_STATION_CONTROLLERS':
            return {
                ...state,
                stationControllers: []
            };

        case 'CREATE_STATION':
            let sc = state.stationControllers.find(x => x.id === action.scId);
            if (sc === undefined) {
                return {
                    ...state
                }
            }

            if (sc.stations.find(x => x.id === action.station.id)) {
                return {
                    ...state
                }
            }

            return {
                ...state,
                stationControllers: state.stationControllers.map(x => {
                    if (x.id === action.scId) {
                        x.stations.push(action.station);
                    }
                    return x;
                })
            };

        case 'DELETE_STATION':
            return {
                ...state,
                stationControllers: state.stationControllers.filter(sc => {
                    return sc.stations.filter(x => {
                        if (x.id !== action.id) {
                            return x;
                        }
                    })
                })
            };

        default:
            return state;
    }
};
