import { Action, Reducer } from 'redux';

export interface Reference
{
    id: number,
    name: string,
    power: number[],
    cartridge: string,
    temperature: number,
    durationTenthsSeconds: number
}

export interface AssistantState {
    references: Reference[],
    nTotalReferences: number
}

export interface CreateReferenceAction { type: 'CREATE_REFERENCE', result: Reference }
export interface DeleteReferenceAction { type: 'DELETE_REFERENCE', result: number }
export interface DeleteAllReferencesAction { type: 'DELETE_ALL_REFERENCES' }

export interface SetTotalReferencesAction { type: 'SET_TOTAL_REFERENCES', result: number }

export type KnownAction =
    | CreateReferenceAction
    | DeleteReferenceAction
    | DeleteAllReferencesAction
    | SetTotalReferencesAction;

export const actionCreators = {
    createReference: (reference: Reference) => ({ type: 'CREATE_REFERENCE', result: reference } as CreateReferenceAction),
    deleteReference: (id: number) => ({ type: 'DELETE_REFERENCE', result: id } as DeleteReferenceAction),
    deleteAllReferences: () => ({ type: 'DELETE_ALL_REFERENCES' } as DeleteAllReferencesAction),
    setTotalReferences: (number: number) => ({ type: 'SET_TOTAL_REFERENCES', result: number } as SetTotalReferencesAction)
};


export const reducer: Reducer<AssistantState> = (
    state: AssistantState | undefined,
    incomingAction: Action
): AssistantState => {
    if (state === undefined) {
        return {
            references: [],
            nTotalReferences: 0
        };
    }

    const action = incomingAction as KnownAction;
    switch (action.type) {
        case 'CREATE_REFERENCE':
            if (state.references.find(x => x.id === action.result.id)) {
                return {
                    ...state
                }
            }

            return {
                ...state,
                references: state.references.concat(action.result)
            };

        case 'DELETE_REFERENCE':
            return {
                ...state,
                references: state.references
                    .filter(x => {
                        if (x.id !== action.result) {
                            return x;
                        }
                    })
            };

        case 'DELETE_ALL_REFERENCES':
            return {
                ...state,
                references: []
            };

        case 'SET_TOTAL_REFERENCES':
            return {
                ...state,
                nTotalReferences: action.result
            };

        default:
            return state;
    }
};
