export function TimeToString(sec_num, format) {

    var showHours = false;
    var padHours = false;
    var showMinutes = false;
    var padMinutes = false;
    var showSeconds = false;
    var padSeconds = false;

    if (format.indexOf("hh") >= 0) { showHours = true; padHours = true; }
    else if (format.indexOf("h") >= 0) { showHours = true; padHours = false; }

    if (format.indexOf("mm") >= 0) { showMinutes = true; padMinutes = true; }
    else if (format.indexOf("m") >= 0) { showMinutes = true; padMinutes = false; }

    if (format.indexOf("ss") >= 0) { showSeconds = true; padSeconds = true; }
    else if (format.indexOf("s") >= 0) { showSeconds = true; padSeconds = false; }

    var hours   = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);

    var result = ""
    
    if (showHours) {
        if (hours < 10 && padHours) { hours = "0" + hours; }
        result += hours;
    }
    if (showMinutes) {
        if (minutes < 10 && padMinutes) { minutes = "0" + minutes; }
        if (result != "") { result += ":"; }
        result += minutes;
    }
    if (showSeconds) {
        if (seconds < 10 && padSeconds) { seconds = "0" + seconds; }
        if (result != "") { result += ":"; }
        result += seconds;
    }

    return result;
}

export function SecToString(sec_num) {
    if (sec_num < 0) {
        return "";
    }

    var hours   = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);

    var result = ""
    
    if (hours != 0) {
        result += hours;
        result += ":";
    }

    if (minutes < 10) { minutes = "0" + minutes; }
    result += minutes;
    result += ":";

    if (seconds < 10) { seconds = "0" + seconds; }
    result += seconds;

    return result;
}

export function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}
