import * as React from 'react';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';

type PaginationNavProps = {
    totalElements: number,
    pageSize: number,
    pageNumber: number,

    handlePageClick: (page: number) => void,
    handlePreviousPageClick: () => void,
    handleNextPageClick: () => void
}

class PaginationNav extends React.PureComponent<PaginationNavProps> {
    public render() {
        const getItems = (pageNumber: number, totalPages: number) => {
            let items = [];

            items.push(
                <PaginationItem>
                    <PaginationLink
                        first
                        disabled={pageNumber === 1}
                        onClick={() => this.props.handlePageClick(1)} />
                </PaginationItem>
            );
            items.push(
                <PaginationItem>
                    <PaginationLink
                        previous
                        disabled={pageNumber === 1}
                        onClick={this.props.handlePreviousPageClick} />
                </PaginationItem>
            );

            items.push(new Array(totalPages).fill(0).map((zero, index) =>
                <PaginationItem active={pageNumber === index + 1}>
                    <PaginationLink onClick={() => this.props.handlePageClick(index + 1)}>
                        {index + 1}
                    </PaginationLink>
                </PaginationItem>
            ));

            items.push(
                <PaginationItem>
                    <PaginationLink
                        next
                        disabled={pageNumber === totalPages}
                        onClick={this.props.handleNextPageClick} />
                </PaginationItem>
            );
            items.push(
                <PaginationItem>
                    <PaginationLink
                        last
                        disabled={pageNumber === totalPages}
                        onClick={() => this.props.handlePageClick(totalPages)} />
                </PaginationItem>
            );

            return items;
        }

        let totalPages = Math.ceil(this.props.totalElements / this.props.pageSize);
        let items = getItems(this.props.pageNumber, totalPages);

        return (
            <Pagination>
                {items}
            </Pagination>
        );
    }
};

export default PaginationNav;
