const DESOLDER = 16;
const EXTRACTOR = 8;
const HIBERNATION = 4;
const SLEEP = 2;
const STAND = 1;
const NONE = 0;

export function GetPortStatus2String(toolStatus, isCalibrating, isSoldering, isPortEnabled) {
    if (!isPortEnabled) {
        return "Port locked";
    }
    else if (toolStatus & EXTRACTOR) {
        return "Tool in the extractor";
    }
    else if (toolStatus & HIBERNATION) {
        return "Tool in hibernation";
    }
    else if (toolStatus & SLEEP) {
        return "Tool in sleep";
    }
    else if (toolStatus & STAND) {
        return "Tool in the stand";
    }
    else if (isCalibrating) {
        return "Calibrating";
    }
    else if (isSoldering) {
        return "Soldering";
    }
    else {
        return "Ready";
    }
}

export function GetPortStatus2Lights(toolStatus, isCalibrating, isSoldering, isPortEnabled) {
    if (!isPortEnabled) {
        return 3;
    }
    else if (toolStatus & EXTRACTOR) {
        return 3;
    }
    else if (toolStatus & HIBERNATION) {
        return 3;
    }
    else if (toolStatus & SLEEP) {
        return 3;
    }
    else if (toolStatus & STAND) {
        return 3;
    }
    else if (isCalibrating) {
        return 2;
    }
    else if (isSoldering) {
        return 1;
    }
    else {
        return 0;
    }
}
