import { Action, Reducer } from 'redux';

export interface TaskConfig
{
    tool: number,
    selected_temp: number,
    change_selected_temp: boolean
}

export interface WorkSolderings
{
    power: number[],
    solderingType: number
}

export interface TaskData
{
    stationId: string,
    stationName: string,
    stationPort: number,
    temperature: number[],
    power: number[],
    workSolderings: WorkSolderings[]
}

export interface TaskHistory
{
    id: number,
    finishDate: Date,
    workConfig: TaskConfig
    workData: TaskData
}

export interface TaskHistoryState {
    taskHistories: TaskHistory[]
}

export interface CreateTaskHistoryAction { type: 'CREATE_TASK_HISTORY', result: TaskHistory }
export interface DeleteTaskHistoryAction { type: 'DELETE_TASK_HISTORY', result: number }
export interface DeleteAllTaskHistoriesAction { type: 'DELETE_ALL_TASK_HISTORIES' }

export type KnownAction =
    | CreateTaskHistoryAction
    | DeleteTaskHistoryAction
    | DeleteAllTaskHistoriesAction;

export const actionCreators = {
    createTaskHistory: (taskHistory: TaskHistory) => ({ type: 'CREATE_TASK_HISTORY', result: taskHistory } as CreateTaskHistoryAction),
    deleteTaskHistory: (id: number) => ({ type: 'DELETE_TASK_HISTORY', result: id } as DeleteTaskHistoryAction),
    deleteAllTaskHistories: () => ({ type: 'DELETE_ALL_TASK_HISTORIES' } as DeleteAllTaskHistoriesAction)
};


export const reducer: Reducer<TaskHistoryState> = (
    state: TaskHistoryState | undefined,
    incomingAction: Action
): TaskHistoryState => {
    if (state === undefined) {
        return { taskHistories: [] };
    }

    const action = incomingAction as KnownAction;
    switch (action.type) {
        case 'CREATE_TASK_HISTORY':
            if (state.taskHistories.find(x => x.id === action.result.id)) {
                return {
                    ...state
                }
            }

            return {
                ...state,
                taskHistories: state.taskHistories.concat(action.result)
            };

        case 'DELETE_TASK_HISTORY':
            return {
                ...state,
                taskHistories: state.taskHistories
                    .filter(x => {
                        if (x.id !== action.result) {
                            return x;
                        }
                    })
            };

        case 'DELETE_ALL_TASK_HISTORIES':
            return {
                ...state,
                taskHistories: []
            };

        default:
            return state;
    }
};
