import * as React from 'react';
import { Button } from 'reactstrap';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import * as StationStore from '../../../store/Station';

type WorkbenchsProps = {
    stationControllers: StationStore.StationController[],
    isLoading: boolean,
    handleSetTraceabilityModeClick: (scUrl: string, newState: boolean) => void
}

class WorkbenchsView extends React.PureComponent<WorkbenchsProps> {
    constructor(props: WorkbenchsProps) {
        super(props);
    }

    public render() {
        const getListOptionsStationControllers = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center mt-3">
                        <Spinner color="danger"/>
                    </div>);
            }

            let options =
                this.props.stationControllers
                    .map(sc => {
                        let items = [];
                        items.push(
                            <tr className="level-1">
                                <td scope="row" className="text-left"><strong>{sc.name}</strong></td>
                                <td></td>
                                <td>
                                    <Button
                                        outline
                                        color={(sc.isQSTMode ? "danger" : "secondary")}
                                        size="sm"
                                        className="pt-0 pb-0"
                                        onClick={() => this.props.handleSetTraceabilityModeClick(sc.url, !sc.isQSTMode)}
                                    >
                                        {sc.isQSTMode ? "Enabled" : "Disabled"}
                                    </Button>
                                </td>
                            </tr>
                        )
                        items.push(
                            sc.stations.map(s => (
                                <tr className="level-2">
                                    <td scope="row" className="pl-3 text-left">{s.name}</td>
                                    <td>{s.model}</td>
                                    <td>
                                        <span className={(sc.isQSTMode && s.isQSTMode) ? "highlight-color" : ""}>{
                                            sc.isQSTMode ?
                                                (s.isQSTMode ? "QST enabled" : "QST disabled" ) :
                                                "--"
                                        }</span>
                                    </td>
                                </tr>
                            ))
                        )
                        return items;
                    })

            return (
                <table className="table table-hover table-sm station-list">
                    <thead>
                        <tr>
                            <th scope="col">Workbench / Station</th>
                            <th scope="col">Station model</th>
                            <th scope="col">Traceability state</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        };

        return (
            <React.Fragment>
                <Row className="mb-2">
                    <h1>Workbenchs</h1>
                </Row>
                <Row>
                    <Card>
                        <CardBody>
                            <CardText>
                                {getListOptionsStationControllers()}
                            </CardText>
                        </CardBody>
                    </Card>
                </Row>
            </React.Fragment>
        );
    }
};

export default WorkbenchsView;
