import * as React from 'react';
import { connect } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { ApplicationState } from '../../../store';
import * as TaskStore from '../../../store/Task';
import TasksService from '../../../services/tasks.service';
import TasksView from './TasksView';

const WAIT_INTERVAL = 1000;

type TasksProps =
    TaskStore.TaskState &
    typeof TaskStore.actionCreators &
    RouteComponentProps<{}>;

interface IState {
    timer: number,
    isLoading: boolean,
    nTotalTasks: number,
    pageSize: number,
    pageNumber: number,
    filter: string
}

class Tasks extends React.PureComponent<TasksProps, IState> {
    constructor(props: TasksProps) {
        super(props);
        this.state = {
            timer: -1,
            isLoading: true,
            nTotalTasks: 0,
            pageSize: 25,
            pageNumber: 1,
            filter: ""
        };

        this.searchTasks(this.props, this.state.filter, this.state.pageNumber);
    }

    searchTasks(props: TasksProps, filter: string, pageNumber: number) {
        props.deleteAllTasks();

        this.setState({
            isLoading: true,
            pageNumber: pageNumber,
            filter: filter
        })

        TasksService
            .getTasks(filter, pageNumber, this.state.pageSize)
            .then(tasks => {
                tasks.forEach((x: TaskStore.Task) =>
                    props.createTask(x)
                )

                this.setState({
                    isLoading: false
                })
            });

        TasksService
            .countTasks(filter)
            .then(value => {
                this.setState({
                    nTotalTasks: value
                })
            });
    }

    public render() {
        const handlePageClick = (pageNumber: number) => {
            this.searchTasks(
                this.props,
                this.state.filter,
                pageNumber);
        }

        const handlePreviousPageClick = () => {
            this.searchTasks(
                this.props,
                this.state.filter,
                Math.max(1, this.state.pageNumber - 1));
        }

        const handleNextPageClick = () => {
            this.searchTasks(
                this.props,
                this.state.filter,
                Math.min(
                    Math.ceil(this.state.nTotalTasks / this.state.pageSize),
                    this.state.pageNumber + 1));
        }

        const handleSearchFieldChange = (filter: string) => {
            if (this.state.timer > 0)
                window.clearTimeout(this.state.timer);

            this.setState({
                pageNumber: 1,
                timer: setTimeout(this.searchTasks.bind(this), WAIT_INTERVAL, this.props, filter, 1)
            });
        }

        return (
            <TasksView
                tasks={this.props.tasks}
                nTotalTasks={this.state.nTotalTasks}
                pageSize={this.state.pageSize}
                pageNumber={this.state.pageNumber}
                isLoading={this.state.isLoading}

                handlePageClick={handlePageClick}
                handlePreviousPageClick={handlePreviousPageClick}
                handleNextPageClick={handleNextPageClick}
                handleSearchFieldChange={handleSearchFieldChange}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        tasks: state.task.tasks
    };
};

export default connect(
    mapStateToProps,
    TaskStore.actionCreators
)(Tasks);
