import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch, AnyAction } from 'redux'
import { RouteComponentProps } from 'react-router';
import { ApplicationState } from '../../../store';
import * as TaskStore from '../../../store/Task';
import * as TaskHistoryStore from '../../../store/TaskHistory';
import TasksService from '../../../services/tasks.service';
import TaskHistoriesService from '../../../services/taskHistories.service';
import { TryParseInt } from '../../../utils/numberUtils';
import TaskDetailView from './TaskDetailView';

type TaskDetailProps = {
    taskActions: typeof TaskStore.actionCreators,
    taskHistoryActions: typeof TaskHistoryStore.actionCreators,
} & TaskStore.TaskState
  & TaskHistoryStore.TaskHistoryState
  & RouteComponentProps<{ id: string }>

interface IState {
    id: number,
    isLoadingTask: boolean,
    isLoadingHistories: boolean,
    nTotalTaskHistories: number,
    pageSize: number,
    pageNumber: number
}

class TaskDetail extends React.PureComponent<TaskDetailProps, IState> {
    constructor(props: TaskDetailProps) {
        super(props);
        const id = TryParseInt(this.props.match.params['id'], 0);

        this.state = {
            id: id,
            isLoadingTask: true,
            isLoadingHistories: true,
            nTotalTaskHistories: 0,
            pageSize: 25,
            pageNumber: 1
        };

        this.searchTask(this.props, id);
        this.searchTaskHistories(this.props, id, this.state.pageNumber);
    }

    searchTask(props: TaskDetailProps, id: string) {
        TasksService
            .getTaskById(id)
            .then(x => {
                props.taskActions.createTask(x)
                
                this.setState({
                    isLoadingTask: false
                })
            });
    }

    searchTaskHistories(props: TaskDetailProps, id: string, pageNumber: number) {
        props.taskHistoryActions.deleteAllTaskHistories();

        TaskHistoriesService
            .getTaskHistories(id, pageNumber, this.state.pageSize)
            .then(taskHistories => {
                taskHistories.forEach((x: TaskHistoryStore.TaskHistory) =>
                    props.taskHistoryActions.createTaskHistory(x)
                )

                this.setState({
                    isLoadingHistories: false,
                    pageNumber: pageNumber
                })
            });

        TaskHistoriesService
            .countTaskHistories(id)
            .then(value => {
                this.setState({
                    nTotalTaskHistories: value
                })
            });
    }

    public render() {
        const handlePageClick = (pageNumber: number) => {
            this.searchTaskHistories(
                this.props,
                this.state.id.toString(),
                pageNumber);
        }

        const handlePreviousPageClick = () => {
            this.searchTaskHistories(
                this.props,
                this.state.id.toString(),
                Math.max(1, this.state.pageNumber - 1));
        }

        const handleNextPageClick = () => {
            this.searchTaskHistories(
                this.props,
                this.state.id.toString(),
                Math.min(
                    Math.ceil(this.state.nTotalTaskHistories / this.state.pageSize),
                    this.state.pageNumber + 1));
        }

        const task = this.props.tasks.find(x => x.id == this.state.id);
        const taskHistories = this.props.taskHistories;

        return (
            <TaskDetailView
                task={task}
                taskHistories={taskHistories}
                nTotalTaskHistories={this.state.nTotalTaskHistories}

                pageSize={this.state.pageSize}
                pageNumber={this.state.pageNumber}

                isLoadingTask={this.state.isLoadingTask}
                isLoadingHistories={this.state.isLoadingHistories}

                handlePageClick={handlePageClick}
                handlePreviousPageClick={handlePreviousPageClick}
                handleNextPageClick={handleNextPageClick}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        tasks: state.task.tasks,
        taskHistories: state.taskHistory.taskHistories
    };
};

const mapDispatchToProps = (dispatch: Dispatch<AnyAction>) => {
    return {
        taskActions: bindActionCreators(TaskStore.actionCreators, dispatch),
        taskHistoryActions: bindActionCreators(TaskHistoryStore.actionCreators, dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TaskDetail);
