import * as React from 'react';
import { ResponsiveContainer, LineChart, Line, XAxis, YAxis, CartesianGrid } from 'recharts';

type PlotProps = {
    plotData: number[]
}

class PlotPowerAxisSimple extends React.PureComponent<PlotProps> {
    public static defaultProps = {
        showLabels: true
    };

    public render() {
        let plotDataObj = this.props.plotData.map((x) => {
            return {power: x}
        });


        return (
            <ResponsiveContainer width='100%' aspect={2.5}>
                <LineChart
                    data={plotDataObj}
                    margin={{
                        left: -61,
                        right: 0
                    }}
                >
                    <XAxis
                        dataKey="tick"
                        height={0} />
                    <YAxis
                        yAxisId="left"
                        orientation="left"
                        domain={['auto', 'auto']} />

                    <Line dataKey="power" yAxisId="left" type="monotone" dot={false}/>
                </LineChart>
            </ResponsiveContainer>
        );
    }
};

export default PlotPowerAxisSimple;
