import * as React from 'react';
import { connect } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Spinner } from 'reactstrap';
import { ApplicationState } from '../../../store';
import * as TaskStore from '../../../store/Task';
import TasksService from '../../../services/tasks.service';
import { TryParseInt } from '../../../utils/numberUtils';
import TaskEditView from './TaskEditView';

type TaskEditProps =
    TaskStore.TaskState &
    typeof TaskStore.actionCreators &
    RouteComponentProps<{id: string}>;

interface IState {
    id: number,
    isLoading: boolean
}

class TaskEdit extends React.PureComponent<TaskEditProps, IState> {
    constructor(props: TaskEditProps) {
        super(props);
        const id = TryParseInt(this.props.match.params['id'], 0);

        this.state = {
            id: id,
            isLoading: true
        };

        this.searchTask(this.props, id);
    }

    searchTask(props: TaskEditProps, id: string) {
        TasksService
            .getTaskById(id)
            .then(x => {
                props.createTask(x)

                this.setState({
                    isLoading: false
                })
            });
    }

    public render() {
        const handleUpdateTask = (task: TaskStore.Task) => {
            TasksService
                .updateTask(task)
                .then(() =>
                    this.props.history.push("/traceability/task/" + this.state.id + "/detail")
                );
        }

        const handleDeleteTask = (id: number) => {
            TasksService
                .deleteTask(id)
                .then(() =>
                    this.props.history.push("/traceability/task")
                );
        }

        if (this.state.isLoading) {
            return (
                <div className="text-center">
                    <Spinner color="danger"/>
                </div>);
        }

        const task = this.props.tasks.find(x => x.id == this.state.id);

        if (task === undefined) {
            return "No item found.";
        }

        return (
            <TaskEditView
                task={task}
                updateTask={handleUpdateTask}
                deleteTask={handleDeleteTask}
            />
        );
    }
};

export default connect(
    (state: ApplicationState) => state.task,
    TaskStore.actionCreators
)(TaskEdit);
