import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Card, CardBody } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'
import { GetPortStatus2String, GetPortStatus2Lights } from '../../../utils/portUtils';
import { IRealTimeData } from './data/IRealTimeData';
import Plot2Axis from '../../content-parts/Plot2Axis';

type WorkProcessProps = {
    taskId: number,
    stationId: string,
    queueId: number,
    currentStep: number,
    plotData: IRealTimeData[],
    counterTenthSecond: number,
    interval: number
}

class WorkProcessStep3 extends React.PureComponent<WorkProcessProps> {
    constructor(props: WorkProcessProps) {
        super(props);
    }

    public render() {
        if (this.props.currentStep !== 3) {
            return null
        }

        let portData = this.props.plotData[this.props.plotData.length - 1];
        let circleLight = "circle-light-" + GetPortStatus2Lights(
            portData.toolStatus,
            portData.isCalibrating,
            portData.isSoldering,
            portData.isPortEnabled);

        return (
            <Row className="mb-2">
                <Card>
                    <CardBody>
                        <Row className="pb-2">
                            <Col xs="auto">
                                Temperature:{' '}
                                <span className="temp-value"> {Math.floor(portData.temperature)} C</span>
                            </Col>
                            <Col>
                                Power:{' '}
                                <span className="power-value">{Math.floor(portData.power)} %</span>
                            </Col>
                            <Col className="text-right">
                                <FontAwesomeIcon icon={faCircle} className={circleLight}/>
                                <strong className="pl-1">{GetPortStatus2String(
                                    portData.toolStatus,
                                    portData.isCalibrating,
                                    portData.isSoldering,
                                    portData.isPortEnabled
                                )}</strong>
                            </Col>
                        </Row>
                        <Row>
                            <Plot2Axis
                                plotData={this.props.plotData}
                                counterTenthSecond={this.props.counterTenthSecond}
                                interval={this.props.interval}
                            />
                        </Row>
                    </CardBody>
                </Card>
            </Row>
        );
    }
};

export default WorkProcessStep3;
