import * as React from 'react';
import { Link } from 'react-router-dom';
import { Col, Row } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { NavLink } from 'reactstrap';

class Home extends React.PureComponent {
    public render() {
        const getProcessoptimization = () => {
            return (
                <NavLink tag={Link} to="/processes/optimization">
                    <Card className="box-shadow-v2">
                        <CardBody>
                            <CardText>
                                Optimize your processes by choosing the best combination of working temperature and the ideal cartridge.
                            </CardText>
                            <CardTitle>Process optimization</CardTitle>
                        </CardBody>
                    </Card>
                </NavLink>
            );
        }

        const getSolderingAssistant = () => {
            return (
                <NavLink tag={Link} to="/processes/assistant">
                    <Card className="box-shadow-v2">
                        <CardBody>
                            <CardText>
                                Pre-stored soldering joint and compare in real-time with all the new soldering processes.
                            </CardText>
                            <CardTitle>Soldering assistant</CardTitle>
                        </CardBody>
                    </Card>
                </NavLink>
            );
        }

        const getTraceability = () => {
            return (
                <NavLink tag={Link} to="/traceability/task">
                    <Card className="box-shadow-v2">
                        <CardBody>
                            <CardText>
                                Record the data of all connected stations.
                            </CardText>
                            <CardTitle>Traceability</CardTitle>
                        </CardBody>
                    </Card>
                </NavLink>
            );
        }

        return (
            <div className="list-cards">
                <Row xs={1} sm={2} md={4}>
                    <Col>{getProcessoptimization()}</Col>
                    <Col>{getSolderingAssistant()}</Col>
                    <Col>{getTraceability()}</Col>
                </Row>
            </div>
        );
    }
};

export default Home;
