import * as React from 'react';
import { Input } from 'reactstrap';
import { InputGroup, InputGroupAddon } from 'reactstrap';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { Spinner } from 'reactstrap';
import * as TaskStore from '../../../store/Task';
import { ToolToString } from '../../../utils/toolUtils';
import PaginationNav from '../../content-components/PaginationNav';

type WorkProcessProps = {
    currentStep: number,
    tasks: TaskStore.Task[],
    nTotalTasks: number,
    pageSize: number,
    pageNumber: number,
    isLoadingTasks: boolean,

    handlePageClick: (pageNumber: number) => void,
    handlePreviousPageClick: () => void,
    handleNextPageClick: () => void,
    handleTaskClick: (task: TaskStore.Task) => void,
    handleSearchFieldChange: (text: string) => void
}

interface IState {
    taskFilter: string[]
}

class WorkProcessStep1 extends React.PureComponent<WorkProcessProps, IState> {
    constructor(props: WorkProcessProps) {
        super(props);
        this.state = {
            taskFilter: []
        };
    }

    public render() {
        if (this.props.currentStep !== 1) {
            return null
        }

        const getListOptionsTasks = () => {
            if (this.props.isLoadingTasks) {
                return (
                    <div className="text-center">
                        <Spinner color="danger"/>
                    </div>);
            }

            let options =
                this.props.tasks.map(x => (
                    <tr onClick={() => this.props.handleTaskClick(x)}>
                        <td scope="row">{x.id}</td>
                        <td>{x.name}</td>
                        <td>{ToolToString(x.workConfig.tool)}</td>
                    </tr>
                ))

            return (
                <table className="table table-hover table-sm table-striped">
                    <thead>
                        <tr>
                            <th scope="col">Code</th>
                            <th scope="col">Name</th>
                            <th scope="col">Tool</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        }

        return (
            <React.Fragment>
                <Row className="mb-4">
                    <InputGroup>
                        <InputGroupAddon addonType="prepend">Search</InputGroupAddon>
                        <Input onChange={(e) => this.props.handleSearchFieldChange(e.target.value)} />
                    </InputGroup>
                </Row>
                <Row className="mb-3">
                    <Card>
                        <CardBody>
                            <CardTitle>Search result ({this.props.nTotalTasks} elements)</CardTitle>
                            <CardText>
                                {getListOptionsTasks()}
                            </CardText>
                        </CardBody>
                    </Card>
                </Row>
                <Row>
                    <PaginationNav
                        totalElements={this.props.nTotalTasks}
                        pageSize={this.props.pageSize}
                        pageNumber={this.props.pageNumber}
                        handlePageClick={this.props.handlePageClick}
                        handlePreviousPageClick={this.props.handlePreviousPageClick}
                        handleNextPageClick={this.props.handleNextPageClick}
                    />
                </Row>
            </React.Fragment>
        );
    }
};

export default WorkProcessStep1;
