import * as React from 'react';
import { ListGroup, ListGroupItem } from 'reactstrap';
import { Spinner } from 'reactstrap';
import * as StationStore from '../../../store/Station';

type StationsProps = {
    stationControllers: StationStore.StationController[],
    isLoading: boolean
}

class StationListSideView extends React.PureComponent<StationsProps> {
    public render() {
        let items = [];
        items.push(<ListGroupItem className="level-0">Stations</ListGroupItem>);
        
        if (this.props.isLoading) {
            items.push(
                <div className="text-center mt-3">
                    <Spinner color="danger"/>
                </div>);
        }
        else {
            for (const sc of this.props.stationControllers) {
                items.push(<ListGroupItem className="level-1"><strong>{sc.name}</strong></ListGroupItem>)

                for (const s of sc.stations) {
                    items.push(<ListGroupItem className="level-2">{s.name} [{s.model}]</ListGroupItem>)
                }
            }
        }

        return (
            <React.Fragment>
                <ListGroup className="station-list">{items}</ListGroup>
            </React.Fragment>
        )
    }
};

export default StationListSideView;
