import * as React from 'react';
import { ResponsiveContainer, LineChart, Line, XAxis, YAxis, CartesianGrid } from 'recharts';

type PlotProps = {
    plotData: IRealTimeData[],
    counterTenthSecond: number,
    interval: number
}

interface IRealTimeData {
    temperature: number,
    power: number
}

class Plot2AxisSimple extends React.PureComponent<PlotProps> {
    public static defaultProps = {
        showLabels: true
    };

    public render() {
        return (
            <ResponsiveContainer width='100%' aspect={2.5}>
                <LineChart
                    data={this.props.plotData}
                    margin={{
                        left: -61,
                        right: -61
                    }}
                >
                    <XAxis
                        dataKey="tick"
                        height={0} />
                    <YAxis
                        yAxisId="left"
                        orientation="left"
                        tick={false}
                        domain={['auto', 'auto']} />
                    <YAxis
                        yAxisId="right"
                        orientation="right"
                        tick={false}
                        domain={['auto', 'auto']} />

                    <Line dataKey="temperature" yAxisId="left" type="monotone" stroke="#dc3545" dot={false} />
                    <Line dataKey="power" yAxisId="right" type="monotone" dot={false}/>
                </LineChart>
            </ResponsiveContainer>
        );
    }
};

export default Plot2AxisSimple;
