import Home from './components/content-pages/Home';
import Processes from './components/content-pages/Processes';
import ProcessOptimization from './components/content-pages/ProcessOptimization';
import SolderingAssistant from './components/content-pages/SolderingAssistant';
import SolderingAssistantNew from './components/content-pages/SolderingAssistantNew';
import Traceability from './components/content-pages/Traceability';
import Tasks from './components/content-pages/Tasks';
import TaskNew from './components/content-pages/TaskNew';
import TaskEdit from './components/content-pages/TaskEdit';
import TaskDetail from './components/content-pages/TaskDetail';
import TaskHistoryDetail from './components/content-pages/TaskHistoryDetail';
import WorkProcess from './components/content-pages/WorkProcess';
import Workbenchs from './components/content-pages/Workbenchs';

export default [
  { path: "/", name: "Home", Component: Home },
  { path: "/processes", name: "Processes", Component: Processes },
  { path: "/processes/optimization", name: "Process optimization", Component: ProcessOptimization },
  { path: "/processes/assistant", name: "Soldering assistant", Component: SolderingAssistant },
  { path: "/processes/assistant/new", name: "New", Component: SolderingAssistantNew },
  { path: "/traceability", name: "Traceability", Component: Traceability },
  { path: "/traceability/task", name: "Tasks", Component: Tasks },
  { path: "/traceability/task/new", name: "New", Component: TaskNew },
  { path: "/traceability/task/:id/detail", name: "Details", Component: TaskDetail },
  { path: "/traceability/task/:id/edit", name: "Edit", Component: TaskEdit },
  { path: "/traceability/task/:taskId/history/:historyId", name: "History", Component: TaskHistoryDetail },
  { path: "/traceability/workprocess", name: "Work process", Component: WorkProcess },
  { path: "/traceability/workbenchs", name: "Workbenchs", Component: Workbenchs }
];
