import * as React from 'react';
import { Container } from 'reactstrap';

class Footer extends React.PureComponent {
    public render() {
        var date = new Date();
        var year = date.getFullYear();

        return (
            <footer className="border-top box-shadow">
                <Container className="clearfix pt-2 pb-2">
                    <div className="float-left">
                        <strong>Copyright</strong> JBC, Soldering S.L.  {year}
                    </div>
                    <div className="float-right">
                    <strong>QST 4.0</strong> - version 0.0.1
                    </div>
                </Container>
            </footer>
        );
    }
}

export default Footer;
