import * as React from 'react';
import { connect } from 'react-redux';
import { Redirect } from "react-router-dom";
import * as TaskStore from '../../../store/Task';
import TasksService from '../../../services/tasks.service';
import TaskNewView from './TaskNewView';

type TaskNewProps = {
}

interface IState {
    redirect: string
}

class TaskNew extends React.PureComponent<TaskNewProps, IState> {
    constructor(props: TaskNewProps) {
        super(props);

        this.state = {
            redirect: ""
        };
    }
    
    public render() {
        const handleNewTask = (task: TaskStore.Task) => {
            TasksService
                .createTask(task);
            
            this.setState({
                redirect: "/traceability/task"
            });
        }

        if (this.state.redirect.length > 0) {
            return <Redirect to={this.state.redirect} />
        }
        
        return (
            <TaskNewView
                newTask={handleNewTask}
            />
        );
    }
};

export default connect(
    null,
    TaskStore.actionCreators
)(TaskNew);
