import * as React from 'react';
import { Jumbotron, Button } from 'reactstrap';

type ProcessOptimizationProps = {
    currentStep: number,
    handleNextClick: () => void
}

class ProcessOptimizationStep1 extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        if (this.props.currentStep !== 1) {
            return null
        }

        const getInformation = () => {
            return (
                <Jumbotron className="pr-opti">
                    <h1 className="display-4">Process optimization</h1>
                    <p className="lead">Optimize your processes by choosing the best combination of working temperature and the ideal cartridge</p>
                    <hr className="my-2" />
                    <p>Extend cartridge life, save time and reduce costs with smart optimization</p>
                    <p className="lead">
                        <Button
                            outline
                            color="primary"
                            size="lg"
                            className="m-2"
                            onClick={this.props.handleNextClick}
                        >Start</Button>
                    </p>
                </Jumbotron>)
        }

        return (
            <React.Fragment>
                {getInformation()}
           </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep1;
