import * as React from 'react';
import { Link } from 'react-router-dom';
import { Row, Button } from 'reactstrap';
import { Modal, ModalHeader, ModalBody, ModalFooter, NavLink } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFile, faTrashAlt } from '@fortawesome/free-regular-svg-icons'
import * as AssistantStore from '../../../store/Assistant';
import PaginationNav from '../../content-components/PaginationNav';
import PlotPowerAxisSimple from '../../content-parts/PlotPowerAxisSimple';

type SolderingAssistantProps = {
    references: AssistantStore.Reference[],
    nTotalReferences: number,
    pageSize: number,
    pageNumber: number,
    isLoading: boolean,

    deleteReference: (id: number) => void,

    handlePageClick: (pageNumber: number) => void,
    handlePreviousPageClick: () => void,
    handleNextPageClick: () => void
}

interface IState {
    showDeleteModal: boolean;
    referenceIdToDelete: number;
}

class SolderingAssistantView extends React.PureComponent<SolderingAssistantProps, IState> {
    constructor(props: SolderingAssistantProps) {
        super(props);

        this.state = {
            showDeleteModal: false,
            referenceIdToDelete: -1
        };
    }

    public render() {
        const getListReferences = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="danger"/>
                    </div>);
            }

            const options = [];

            for (const value of this.props.references) {
                options.push(
                    <tr>
                        <td scope="row">{value.id}</td>
                        <td>
                            <PlotPowerAxisSimple
                                plotData={value.power}
                            />
                        </td>
                        <td>{value.name}</td>
                        <td>{value.cartridge}</td>
                        <td>{value.temperature / 9}</td>
                        <td>{value.durationTenthsSeconds / 10}</td>
                        <td>
                            <a href="#" className="p-0" onClick={(event) => handleDeleteClick(event, value.id)}>
                                <FontAwesomeIcon icon={faTrashAlt} className="fa"/>
                            </a>
                        </td>
                    </tr>)
            }

            return (
                <table className="table table-hover table-sm table-striped">
                    <thead>
                        <tr>
                            <th scope="col">Code</th>
                            <th scope="col" className="col-3">Reference</th>
                            <th scope="col">Name</th>
                            <th scope="col">Cartridge</th>
                            <th scope="col">Temperature</th>
                            <th scope="col">Duration</th>
                            <th scope="col">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        }

        const handleDeleteClick = (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>, id: number) => {
            event.preventDefault();

            this.setState({
                referenceIdToDelete: id,
                showDeleteModal: true
            });
        };

        const handleDeleteConfirmClick = () => {
            this.props.deleteReference(this.state.referenceIdToDelete);
            this.setState({
                showDeleteModal: false
            });
        };

        const handleDeleteCancelClick = () => {
            this.setState({
                showDeleteModal: false
            });
        };

        return (
            <React.Fragment>
                <Row className="mb-4">
                    <h1>List of references</h1>

                    <Button outline color="danger" size="sm" className="m-2 ml-4 float-right">
                        <NavLink tag={Link} className="p-0" to="/processes/assistant/new">
                            <FontAwesomeIcon icon={faFile} /> Create new
                        </NavLink>
                    </Button>
                </Row>
                <Row className="mb-3">
                    <Card>
                        <CardBody>
                            <CardText>
                                {getListReferences()}
                            </CardText>
                        </CardBody>
                    </Card>
                </Row>
                <Row>
                    <PaginationNav
                        totalElements={this.props.nTotalReferences}
                        pageSize={this.props.pageSize}
                        pageNumber={this.props.pageNumber}
                        handlePageClick={this.props.handlePageClick}
                        handlePreviousPageClick={this.props.handlePreviousPageClick}
                        handleNextPageClick={this.props.handleNextPageClick}
                    />
                </Row>

                <Modal isOpen={this.state.showDeleteModal} toggle={() => handleDeleteCancelClick()}>
                    <ModalHeader toggle={() => handleDeleteCancelClick()}>Delete reference</ModalHeader>
                    <ModalBody>Are you sure to delete the reference? This action can not be undone.</ModalBody>
                    <ModalFooter>
                        <Button outline color="danger" onClick={() => handleDeleteConfirmClick()}>Confirm</Button>{' '}
                        <Button color="danger" onClick={() => handleDeleteCancelClick()}>Cancel</Button>
                    </ModalFooter>
                </Modal>
            </React.Fragment>
        );
    }
};

export default SolderingAssistantView;
