import * as React from 'react';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import * as StationStore from '../../../store/Station';
import { ToolToString } from '../../../utils/toolUtils';

type WorkProcessProps = {
    stationControllers: StationStore.StationController[],
    taskTool: number,
    currentStep: number,
    handleStationClick: (station: StationStore.Station, port: number) => void
}

class WorkProcessStep2 extends React.PureComponent<WorkProcessProps> {
    constructor(props: WorkProcessProps) {
        super(props);
    }

    public render() {
        if (this.props.currentStep !== 2) {
            return null
        }

        const getListOptionsStationControllers = () => {
            let options =
                this.props.stationControllers
                    .filter(sc => (
                        sc.stations.some(s => (
                            s.ports.some(p => (
                                p.tool == this.props.taskTool
                            ))
                        ))
                    ))
                    .map(sc => (
                        sc.stations.map(s => (
                            s.ports
                            .filter(p => (
                                p.tool == this.props.taskTool
                            ))
                            .map(p => (
                                <tr onClick={() => this.props.handleStationClick(s, p.id)}>
                                    <td scope="row">{s.name}</td>
                                    <td>{p.id + 1}</td>
                                    <td>{ToolToString(p.tool)}</td>
                                </tr>
                            ))
                        ))
                    ))

            return (
                <table className="table table-hover table-sm table-striped">
                    <thead>
                        <tr>
                            <th scope="col">Station name</th>
                            <th scope="col">Port</th>
                            <th scope="col">Tool</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        };

        return (
            <React.Fragment>
                <Row>
                    <Card>
                        <CardBody>
                            <CardTitle>Stations available</CardTitle>
                            <CardText>
                                {getListOptionsStationControllers()}
                            </CardText>
                        </CardBody>
                    </Card>
                </Row>
            </React.Fragment>
        );
    }
};

export default WorkProcessStep2;
