import T470data from './cartridgeData/T470';
import T245data from './cartridgeData/T245';
import T210data from './cartridgeData/T210';
import PA120data from './cartridgeData/PA120';
import NT105data from './cartridgeData/NT105';
import NP105data from './cartridgeData/NP105';
import HT420data from './cartridgeData/HT420';
import DS360data from './cartridgeData/DS360';
import DR560data from './cartridgeData/DR560';
import AP250data from './cartridgeData/AP250';

interface CartridgeList {
    N: string;
    G: string;
    RT: string;
    P300: string;
    P400: string;
}

interface CartridgeGroup {
    G: string;
    P300: string;
    P400: string;
}

interface CartridgeData {
    F: string;
    Default: string;
    Cartridge_list: CartridgeList[];
    Cartridge_group: CartridgeGroup[];
}

interface CartridgeRoot {
    _Type: string;
    _V: number;
    _: CartridgeData;
}

export interface ICartridge {
    tool: number,
    fullName: string,
    family: string,
    name: string,
    isDefault: boolean,
    img: string
}

export function LoadCartridges() {
    let cartridges: ICartridge[] = [];
    cartridges = cartridges.concat(MapCatridges(T470data));
    cartridges = cartridges.concat(MapCatridges(T245data));
    cartridges = cartridges.concat(MapCatridges(T210data));
    cartridges = cartridges.concat(MapCatridges(PA120data));
    cartridges = cartridges.concat(MapCatridges(NT105data));
    cartridges = cartridges.concat(MapCatridges(NP105data));
    cartridges = cartridges.concat(MapCatridges(HT420data));
    cartridges = cartridges.concat(MapCatridges(DS360data));
    cartridges = cartridges.concat(MapCatridges(DR560data));
    cartridges = cartridges.concat(MapCatridges(AP250data));

    return cartridges;
}

export function MapCatridges(cartridgeRoot: CartridgeRoot) {
    let result: ICartridge[] = [];

    let cartridge = cartridgeRoot._;
    let family = cartridge.F;
    let def = cartridge.Default;

    cartridge.Cartridge_list.forEach(item => {
        result.push({
            tool: GetToolIdByFamily(family),
            fullName: family + item.N,
            family: family,
            name: item.N,
            isDefault: def === item.N,
            img: CartridgeImagePath(family, item.N)
        });
    })

    return result;
};

export function GetToolIdByFamily(family: string)
{
    family = family.toLowerCase();

    if (family === "c210") return 1; // T210
    if (family === "c245") return 2; // T245
    if (family === "c120") return 3; // PA
    if (family === "c420") return 4; // HT
    if (family === "c360") return 5; // DS
    if (family === "c560") return 6; // DR
    if (family === "c105" || family === "c115") return 7; // NT105, NP105
    if (family === "c470") return 9; // T470

    return 0;
}

export function CartridgeImagePath(family: string, name: string)
{
    return "img/cartidges/" + family.toLowerCase() + name.toLowerCase() + "_detail.png";
}

export function CartridgeImagePathByFullName(name: string)
{
    let cartridge = LoadCartridges().find(x => x.fullName.toLowerCase() === name.toLowerCase());

    if (cartridge === undefined) {
        return "";
    }

    return CartridgeImagePath(cartridge.family, cartridge.name);
}
