import { Action, Reducer } from 'redux';

export interface TaskConfig
{
    tool: number,
    selectedTemp: number,
    changeSelectedTemp: boolean
}

export interface Task
{
    id: number,
    name: string,
    workConfig: TaskConfig,
    timesPerformed: number
}

export interface TaskState {
    tasks: Task[],
    nTotalTasks: number
}

export interface CreateTaskAction { type: 'CREATE_TASK', result: Task }
export interface DeleteTaskAction { type: 'DELETE_TASK', result: number }
export interface DeleteAllTasksAction { type: 'DELETE_ALL_TASKS' }

export interface SetTotalTasksAction { type: 'SET_TOTAL_TASKS', result: number }

export type KnownAction =
    | CreateTaskAction
    | DeleteTaskAction
    | DeleteAllTasksAction
    | SetTotalTasksAction;

export const actionCreators = {
    createTask: (task: Task) => ({ type: 'CREATE_TASK', result: task } as CreateTaskAction),
    deleteTask: (id: number) => ({ type: 'DELETE_TASK', result: id } as DeleteTaskAction),
    deleteAllTasks: () => ({ type: 'DELETE_ALL_TASKS' } as DeleteAllTasksAction),
    setTotalTasks: (number: number) => ({ type: 'SET_TOTAL_TASKS', result: number } as SetTotalTasksAction)
};


export const reducer: Reducer<TaskState> = (
    state: TaskState | undefined,
    incomingAction: Action
): TaskState => {
    if (state === undefined) {
        return {
            tasks: [],
            nTotalTasks: 0
        };
    }

    const action = incomingAction as KnownAction;
    switch (action.type) {
        case 'CREATE_TASK':
            if (state.tasks.find(x => x.id === action.result.id)) {
                return {
                    ...state
                }
            }

            return {
                ...state,
                tasks: state.tasks.concat(action.result)
            };

        case 'DELETE_TASK':
            return {
                ...state,
                tasks: state.tasks
                    .filter(x => {
                        if (x.id !== action.result) {
                            return x;
                        }
                    })
            };

        case 'DELETE_ALL_TASKS':
            return {
                ...state,
                tasks: []
            };

        case 'SET_TOTAL_TASKS':
            return {
                ...state,
                nTotalTasks: action.result
            };

        default:
            return state;
    }
};
