export function GetPlotSeriesData(temperature, power) {
    let plotData = [];

    for (var i = 0; i < temperature.length; i++) {
        plotData.push({
            tick: i,
            temperature: temperature[i] / 9,
            power: power[i] / 10
        });
    }

    return plotData;
}

export function EmptyPlotSeriesData(maxPLotPoints) {
    let data = [];

    for (var i = 0; i < maxPLotPoints; i++) {
        data.push({
            tick: -1,
            temperature: 0,
            power: 0,
            toolStatus: 0,
            isPortEnabled: true,
            isCalibrating: false,
            isSoldering: false,
            tenthsOfSecondSoldering: 0
        });
    }

    return data;
}

export function PushPlotSeriesData(data, realTimeData, maxPLotPoints = 100) {
    data.push({
        tick: data.length > 0 ? data[data.length - 1].tick + 1 : 0,
        temperature: realTimeData.temperature / 9,
        power: realTimeData.power / 10,
        toolStatus: realTimeData.toolStatus,
        isPortEnabled: realTimeData.isPortEnabled,
        isCalibrating: realTimeData.isCalibrating,
        isSoldering: realTimeData.isSoldering
    });

    if (maxPLotPoints > 0 && maxPLotPoints < data.length) {
        data.splice(0, data.length - maxPLotPoints);
    }
}
