import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Button } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { ResponsiveContainer, LineChart, Line, XAxis, YAxis, CartesianGrid, ReferenceArea } from 'recharts';
import { CartridgeImagePathByFullName } from '../../../utils/cartridgeUtils';
import { IOptimizedSoldering } from './data/IOptimizedSoldering';

type ProcessOptimizationProps = {
    currentStep: number,
    isLoading: boolean,
    optimizedSoldering: IOptimizedSoldering,
    selectedCartridge: string,
    handleNextClick: () => void
}

class ProcessOptimizationStep5 extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        if (this.props.currentStep !== 5) {
            return null
        }

        const getPlot = (temperature: number) => {
            return (
                <ResponsiveContainer width='100%' aspect={2.5}>
                    <LineChart
                        data={this.props.optimizedSoldering.points}
                        margin={{
                            left: -10,
                            right: -10
                        }}
                    >
                        <CartesianGrid stroke="#e6e6e6" />

                        <XAxis
                            dataKey="temperature"
                            height={50}
                            padding={{ left: 30, right: 30 }}
                            label="Temperature"
                            tickFormatter={(value) => Math.floor(value / 9)}
                            domain={[250 * 9, 400 * 9]}
                            ticks={[250 * 9, 300 * 9, 350 * 9, 400 * 9]}
                            type="number" />
                        <YAxis
                            orientation="left"
                            label="Tip life"
                            tickFormatter={(value) => value + "x"}
                            domain={[0, 3]}
                            ticks={[1, 2, 3]}
                            type="number" />
    
                        <Line dataKey="time" type="monotone" stroke="#dc3545" dot={false} />
                        <ReferenceArea
                            x1={temperature - 20 * 9} x2={temperature + 20 * 9}
                            y1={0} y2={3}
                            fill="#fff8e1" />
                        <ReferenceArea
                            x1={temperature + 20 * 9} x2={400 * 9}
                            y1={0} y2={3}
                            fill="#ffebeb" />
                        <ReferenceArea
                            x1={280 * 9} x2={temperature - 20 * 9}
                            y1={0} y2={3}
                            fill="#ebebff" />
                    </LineChart>
                </ResponsiveContainer>
            );
        }

        const getOptimizedSolderingPlotCard = () => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody = 
                    <div className="text-center mt-3">
                        <Spinner color="danger"/>
                    </div>;
            }
            else {
                cardBody = getPlot(this.props.optimizedSoldering.recommended.temperature)
            }

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Performance curve</CardTitle>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        const getDetailsCard = () => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody = 
                    <div className="text-center mt-3">
                        <Spinner color="danger"/>
                    </div>;
            }
            else {
                cardBody =
                    <Row>
                        <Col xs="6">
                            <CardText>Cartridge: <strong>{this.props.selectedCartridge}</strong></CardText>

                            <CardText>
                                <strong>Optimized:</strong>
                            </CardText>
                            <CardText>Tip live: {this.props.optimizedSoldering.recommended.time}x</CardText>
                            <CardText>Temperature: {Math.floor(this.props.optimizedSoldering.recommended.temperature / 9)} C</CardText>

                            <CardText>
                                <strong>Better performance:</strong>
                            </CardText>
                            <CardText>Tip live: {this.props.optimizedSoldering.performance.time}x
                            </CardText>
                            <CardText>Temperature: {Math.floor(this.props.optimizedSoldering.performance.temperature / 9)} C
                            </CardText>

                            <CardText>
                                <strong>Better durability:</strong>
                            </CardText>
                            <CardText>Tip live: {this.props.optimizedSoldering.durability.time}x</CardText>
                            <CardText>Temperature: {Math.floor(this.props.optimizedSoldering.durability.temperature / 9)} C
                            </CardText>
                        </Col>
                        <Col xs="6">
                            <img src={CartridgeImagePathByFullName(this.props.selectedCartridge)} />
                        </Col>
                    </Row>
            }

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Performance result</CardTitle>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        const getNextButton = () => {
            return (
                <Button
                    outline
                    color="danger"
                    size="sm"
                    className="m-2"
                    onClick={this.props.handleNextClick}
                >Start again</Button>)
        }

        return (
            <React.Fragment>
                <Row className="mb-1">
                    <h1>Process optimization</h1>
                </Row>
                <Row className="mb-3">
                    <Col xs="8" className="pl-0">
                        {getOptimizedSolderingPlotCard()}
                    </Col>
                    <Col xs="4" className="d-flex flex-column">
                        {getDetailsCard()}
                    </Col>
                </Row>
                <Row>
                    {getNextButton()}
                </Row>
           </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep5;
