import * as React from 'react';
import { Alert } from 'reactstrap';
import { Col, Row } from 'reactstrap';
import { Button, Input } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons'
import * as StationStore from '../../../store/Station';
import { LoadCartridges, ICartridge } from '../../../utils/cartridgeUtils';
import Plot2AxisSimple from '../../content-parts/Plot2AxisSimple';
import PortStatusCard from '../../content-parts/PortStatusCard';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingData } from './data/ISolderingData';

type ProcessOptimizationProps = {
    currentStep: number,
    
    selectedStation: StationStore.Station,
    selectedPort: number,
    selectedTool: number,
    selectedTemperature: number,

    lastRealTimeData: IRealTimeData,
    counterTenthSecond: number,

    detectedSolderings: ISolderingData[],

    handleCartridgeChange: (value: string) => void,
    handleDeleteRealTimeData: (value: string) => void,
    handleNextClick: () => void
}

interface IState {
    cartridges: ICartridge[],
    cartridgeImagePath: string
}

class ProcessOptimizationStep3 extends React.PureComponent<ProcessOptimizationProps, IState> {
    constructor(props: ProcessOptimizationProps) {
        super(props);
        
        let cartridgeImagePath = "";
        let cartridges = LoadCartridges();
        let defaultCartridge = cartridges.find(x => x.isDefault && x.tool === this.props.selectedTool);
        
        if (defaultCartridge !== undefined) {
            cartridgeImagePath = defaultCartridge.img;
            this.props.handleCartridgeChange(defaultCartridge.fullName);
        }

        this.state = {
            cartridges: cartridges,
            cartridgeImagePath: cartridgeImagePath
        };
    }
    
    public render() {
        if (this.props.currentStep !== 3) {
            return null
        }

        const getAlerts = () => {
            let alerts = [];

            alerts.push(
                <Alert color="warning">
                    Performs multiple solderings with different cartridges
                </Alert>);

            return alerts;
        }

        const getPortStatus = () => {
            return <PortStatusCard portData={this.props.lastRealTimeData} />;
        }

        const getDetailsCard = () => {
            let stationDetail;
            if (this.props.selectedStation.id != "") {
                stationDetail =
                    <React.Fragment>
                        <CardText>Station Id: {this.props.selectedStation.id}</CardText>
                        <CardText>Station: {this.props.selectedStation.name} [{this.props.selectedStation.model}]</CardText>
                        <CardText>Port: {this.props.selectedPort + 1}</CardText>
                    </React.Fragment>
            }

            let card =
                <Card>
                    <CardBody>
                        <CardTitle>Details</CardTitle>
                        {stationDetail}
                    </CardBody>
                </Card>

            return (
                <React.Fragment>
                    {card}
                </React.Fragment>
            )
        };
        
        const getCartridges = () => {
            let cartridges = this.state.cartridges
                .filter(x => x.tool === this.props.selectedTool)
                .map(x => (
                    <option selected={x.isDefault}>{x.fullName}</option>
                ));

            return cartridges;
        }

        const handleCartridgeChange = (value: string) => {
            this.props.handleCartridgeChange(value);
            let cartridge = this.state.cartridges.find(x => x.fullName === value);
            let cartridgeImagePath = cartridge === undefined ? "" : cartridge.img;

            this.setState({
                cartridgeImagePath: cartridgeImagePath
            });
        }

        const getConfigurationCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardTitle>Configuration</CardTitle>
                        <Row>
                            <Col xs="6">
                                <CardText>Selected temperature: {this.props.selectedTemperature / 9} C</CardText>
                                <CardText>
                                    <strong>Selected cartridge:</strong>
                                    <Input
                                        type="select"
                                        onChange={(e) => handleCartridgeChange(e.target.value)}
                                    >
                                        {getCartridges()}
                                    </Input>
                                </CardText>
                            </Col>
                            <Col xs="6">
                                <img src={this.state.cartridgeImagePath} />
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getRegisteredPlotsCard = () => {
            let registeredPlots = this.props.detectedSolderings.map(x => {
                let data: IRealTimeData[];
                data = x.data.map(x => {
                    return {
                        temperature: x.temperature,
                        power: x.power,
                        toolStatus: x.toolStatus,
                        isPortEnabled: x.isPortEnabled,
                        isCalibrating: x.isCalibrating,
                        isSoldering: x.isSoldering,
                        tenthsOfSecondSoldering: x.tenthsOfSecondSoldering
                    }
                });

                return (
                    <Col>
                        <Card className="pr-opti-plot">
                            <CardBody>
                                <Row>
                                    <Col>
                                        <CardText>Time: <strong>{x.solderingResult.tenthsOfSecondSoldering / 10} seconds</strong></CardText>
                                        <CardText>Energy: <strong>{x.solderingResult.energy} ws</strong></CardText>
                                        <CardText>Cartridge: <strong>{x.cartridge}</strong></CardText>
                                    </Col>
                                    <Col className="text-right">
                                        <a className="pointer" onClick={() => this.props.handleDeleteRealTimeData(x.date)}>
                                            <FontAwesomeIcon icon={faTrashAlt} />
                                        </a>
                                    </Col>
                                </Row>
                                <Plot2AxisSimple
                                    plotData={data}
                                    counterTenthSecond={x.data.length}
                                    interval={Math.floor(x.data.length / 2)}
                                />
                            </CardBody>
                        </Card>
                    </Col>);
            });

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Registered processes</CardTitle>
                        <Row xs={1} sm={2} md={3}>
                            {registeredPlots}
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getNextButton = () => {
            if (this.props.detectedSolderings.length === 0) {
                return "";
            }

            return (
                <Button
                    outline
                    color="danger"
                    size="sm"
                    className="m-2"
                    onClick={this.props.handleNextClick}
                >Continue</Button>)
        }

        return (
            <React.Fragment>
                <Row className="mb-1">
                    <h1>Process optimization</h1>
                </Row>
                <Row className="mb-1">
                    <h4>Step 2 - Cartridge selection</h4>
                </Row>
                <Row className="mb-1">
                    {getAlerts()}
                </Row>
                <Row className="mb-3">
                    {getPortStatus()}
                </Row>
                <Row className="mb-3">
                    <Col xs="6" className="pl-0">
                        {getDetailsCard()}
                    </Col>
                    <Col xs="6" className="pl-0 pr-0">
                        {getConfigurationCard()}
                    </Col>
                </Row>
                <Row>
                    {getRegisteredPlotsCard()}
                </Row>
                <Row>
                    {getNextButton()}
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep3;
