import * as React from 'react';
import { ResponsiveContainer, LineChart, Line, XAxis, YAxis, CartesianGrid } from 'recharts';
import { SecToString } from '../../../utils/timeUtils';

type PlotProps = {
    plotData: IRealTimeData[],
    counterTenthSecond: number,
    interval: number
}

interface IRealTimeData {
    temperature: number,
    power: number
}

class Plot2Axis extends React.PureComponent<PlotProps> {
    public static defaultProps = {
        showLabels: true
    };

    public render() {
        return (
            <ResponsiveContainer width='100%' aspect={2.5}>
                <LineChart
                    data={this.props.plotData}
                    margin={{
                        left: -10,
                        right: -10
                    }}
                >
                    <CartesianGrid stroke="#e6e6e6" />

                    <XAxis
                        dataKey="tick"
                        height={50}
                        label={"Time (s)"}
                        tickFormatter={(tick) => SecToString(Math.floor(tick / 10))}
                        interval={this.props.interval} />
                    <YAxis
                        yAxisId="left"
                        orientation="left"
                        domain={[0, 400]} />
                    <YAxis
                        yAxisId="right"
                        orientation="right"
                        domain={[0, 100]} />

                    <Line dataKey="temperature" yAxisId="left" type="monotone" stroke="#dc3545" dot={false} />
                    <Line dataKey="power" yAxisId="right" type="monotone" dot={false}/>
                </LineChart>
            </ResponsiveContainer>
        );
    }
};

export default Plot2Axis;
