import Configuration from '../configuration';
import FakeService from './fake.service';
import RestService from './restService';

class TasksService {
    static async countTasks(filter) {
        let count;

        if (Configuration.FakeMode) {
            count = FakeService.countTasks();
        }
        else {
            count = RestService.countTasks(filter);
        }

        return count;
    }

    static async getTasks(filter, pageNumber, pageSize) {
        let tasks;

        if (Configuration.FakeMode) {
            tasks = FakeService.getTasks();
        }
        else {
            tasks = RestService.getTasks(filter, pageNumber, pageSize);
        }

        return tasks;
    }

    static async getTaskById(id) {
        let task;

        if (Configuration.FakeMode) {
            task = FakeService.getTaskById();
        }
        else {
            task = RestService.getTaskById(id);
        }

        return task;
    }

    static async createTask(task) {
        if (Configuration.FakeMode) {
            FakeService.createTask();
        }
        else {
            RestService.createTask(task);
        }
    }

    static async updateTask(task) {
        if (Configuration.FakeMode) {
            FakeService.updateTask();
        }
        else {
            RestService.updateTask(task);
        }
    }

    static async deleteTask(id) {
        if (Configuration.FakeMode) {
            FakeService.deleteTask();
        }
        else {
            RestService.deleteTask(id);
        }
    }
}

export default TasksService;
