import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch, AnyAction } from 'redux'
import { RouteComponentProps } from 'react-router';
import { Spinner } from 'reactstrap';
import { ApplicationState } from '../../../store';
import * as TaskStore from '../../../store/Task';
import * as TaskHistoryStore from '../../../store/TaskHistory';
import TasksService from '../../../services/tasks.service';
import TaskHistoriesService from '../../../services/taskHistories.service';
import { TryParseInt } from '../../../utils/numberUtils';
import TaskHistoryDetailView from './TaskHistoryDetailView';

type TaskHistoryDetailProps = {
    taskActions: typeof TaskStore.actionCreators,
    taskHistoryActions: typeof TaskHistoryStore.actionCreators,
} & TaskStore.TaskState
  & TaskHistoryStore.TaskHistoryState
  & RouteComponentProps<{ taskId: string, historyId: string }>

interface IState {
    taskId: number,
    taskHistoryId: number,
    isLoading: boolean
}

class TaskHistoryDetail extends React.PureComponent<TaskHistoryDetailProps, IState> {
    constructor(props: TaskHistoryDetailProps) {
        super(props);
        const taskId = TryParseInt(this.props.match.params['taskId'], 0);
        const taskHistoryId = TryParseInt(this.props.match.params['historyId'], 0);

        this.state = {
            taskId: taskId,
            taskHistoryId: taskHistoryId,
            isLoading: true
        };

        this.searchTask(this.props, taskId);
        this.searchTaskHistories(this.props, taskHistoryId);
    }

    searchTask(props: TaskHistoryDetailProps, id: string) {
        TasksService
            .getTaskById(id)
            .then(x =>
                props.taskActions.createTask(x)
            );
    }

    searchTaskHistories(props: TaskHistoryDetailProps, id: string) {
        TaskHistoriesService
            .getTaskHistoryById(id)
            .then(x => {
                props.taskHistoryActions.createTaskHistory(x)

                this.setState({
                    isLoading: false
                })
            });
    }

    public render() {
        const task = this.props.tasks.find(x => x.id == this.state.taskId);
        const taskHistory = this.props.taskHistories.find(x => x.id == this.state.taskHistoryId);

        if (this.state.isLoading) {
            return (
                <div className="text-center">
                    <Spinner color="danger"/>
                </div>);
        }

        if (task === undefined || taskHistory === undefined) {
            return "No item founded.";
        }

        return (
            <TaskHistoryDetailView
                task={task}
                taskHistory={taskHistory}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        tasks: state.task.tasks,
        taskHistories: state.taskHistory.taskHistories
    };
};

const mapDispatchToProps = (dispatch: Dispatch<AnyAction>) => {
    return {
        taskActions: bindActionCreators(TaskStore.actionCreators, dispatch),
        taskHistoryActions: bindActionCreators(TaskHistoryStore.actionCreators, dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TaskHistoryDetail);
