import * as React from 'react';
import { Alert } from 'reactstrap';
import { Col, Row, Input } from 'reactstrap';
import { Button } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons'
import * as StationStore from '../../../store/Station';
import { CartridgeImagePathByFullName } from '../../../utils/cartridgeUtils';
import Plot2AxisSimple from '../../content-parts/Plot2AxisSimple';
import PortStatusCard from '../../content-parts/PortStatusCard';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingData } from './data/ISolderingData';

type ProcessOptimizationProps = {
    currentStep: number,
    isLoading: boolean,
    
    selectedStation: StationStore.Station,
    selectedPort: number,
    selectedTemperature: number,

    lastRealTimeData: IRealTimeData,
    counterTenthSecond: number,

    detectedSolderings: ISolderingData[],
    fasterSoldering: ISolderingData,

    handleSelecTempChange: (temperature: string) => void,
    handleDeleteRealTimeData: (value: string) => void,
    handleNextClick: () => void
}

class ProcessOptimizationStep4 extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        if (this.props.currentStep !== 4) {
            return null
        }

        const getAlerts = () => {
            let alerts = [];

            alerts.push(
                <Alert color="warning">
                    Performs multiple solderings with different temperatures and the selected cartridge
                </Alert>);

            return alerts;
        }

        const getPortStatus = () => {
            return <PortStatusCard portData={this.props.lastRealTimeData} />;
        }

        const getDetailsCard = () => {
            let stationDetail;
            if (this.props.selectedStation.id != "") {
                stationDetail =
                    <React.Fragment>
                        <CardText>Station Id: {this.props.selectedStation.id}</CardText>
                        <CardText>Station: {this.props.selectedStation.name} [{this.props.selectedStation.model}]</CardText>
                        <CardText>Port: {this.props.selectedPort + 1}</CardText>
                    </React.Fragment>
            }

            let card =
                <Card>
                    <CardBody>
                        <CardTitle>Details</CardTitle>
                        {stationDetail}
                    </CardBody>
                </Card>

            return (
                <React.Fragment>
                    {card}
                </React.Fragment>
            )
        };

        const getConfigurationCard = () => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody = 
                    <div className="text-center mt-3">
                        <Spinner color="danger"/>
                    </div>;
            }
            else {
                cardBody =
                    <Row>
                        <Col xs="6">
                            <CardText>Selected cartridge: {this.props.fasterSoldering.cartridge}</CardText>
                            <CardText>
                                <strong>Selected temperature:</strong>
                                <Input
                                    type="select"
                                    defaultValue={this.props.selectedTemperature / 9}
                                    onChange={(e) => this.props.handleSelecTempChange(e.target.value)}
                                >
                                    <option>425</option>
                                    <option>400</option>
                                    <option>375</option>
                                    <option>350</option>
                                    <option>325</option>
                                    <option>300</option>
                                </Input>
                            </CardText>
                        </Col>
                        <Col xs="6">
                            <img src={CartridgeImagePathByFullName(this.props.fasterSoldering.cartridge)} />
                        </Col>
                    </Row>
            }

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Configuration</CardTitle>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        const getRegisteredPlotsCard = () => {
            let registeredPlots = this.props.detectedSolderings.map(x => {
                let data: IRealTimeData[];
                data = x.data.map(x => {
                    return {
                        temperature: x.temperature,
                        power: x.power,
                        toolStatus: x.toolStatus,
                        isPortEnabled: x.isPortEnabled,
                        isCalibrating: x.isCalibrating,
                        isSoldering: x.isSoldering,
                        tenthsOfSecondSoldering: x.tenthsOfSecondSoldering
                    }
                });

                return (
                    <Col>
                        <Card className="pr-opti-plot">
                            <CardBody>
                                <Row>
                                    <Col>
                                        <CardText>Time: <strong>{x.solderingResult.tenthsOfSecondSoldering / 10} seconds</strong></CardText>
                                        <CardText>Energy: <strong>{x.solderingResult.energy} ws</strong></CardText>
                                        <CardText>Selected temperature: <strong>{x.selectedTemperature / 9} C</strong></CardText>
                                    </Col>
                                    <Col className="text-right">
                                        <a className="pointer" onClick={() => this.props.handleDeleteRealTimeData(x.date)}>
                                            <FontAwesomeIcon icon={faTrashAlt} />
                                        </a>
                                    </Col>
                                </Row>
                                <Plot2AxisSimple
                                    plotData={data}
                                    counterTenthSecond={x.data.length}
                                    interval={Math.floor(x.data.length / 2)}
                                />
                            </CardBody>
                        </Card>
                    </Col>);
            });

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Registered processes</CardTitle>
                        <Row xs={1} sm={2} md={3}>
                            {registeredPlots}
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getNextButton = () => {
            if (this.props.detectedSolderings.length === 0) {
                return "";
            }

            return (
                <Button
                    outline
                    color="danger"
                    size="sm"
                    className="m-2"
                    onClick={this.props.handleNextClick}
                >Continue</Button>)
        }

        return (
            <React.Fragment>
                <Row className="mb-1">
                    <h1>Process optimization</h1>
                </Row>
                <Row className="mb-1">
                    <h4>Step 3 - Temperature selection</h4>
                </Row>
                <Row className="mb-1">
                    {getAlerts()}
                </Row>
                <Row className="mb-3">
                    {getPortStatus()}
                </Row>
                <Row className="mb-3">
                    <Col xs="6" className="pl-0">
                        {getDetailsCard()}
                    </Col>
                    <Col xs="6" className="pl-0 pr-0">
                        {getConfigurationCard()}
                    </Col>
                </Row>
                <Row>
                    {getRegisteredPlotsCard()}
                </Row>
                <Row>
                    {getNextButton()}
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep4;
