import React from "react";
import { Link } from 'react-router-dom';
import { Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { Container } from 'reactstrap';
import { NavLink } from 'reactstrap';

interface ICrumb {
    name: string,
    path: string
}

type BreadcrumbsProps = {
    crumbs: ICrumb[]
}

class Breadcrumbs extends React.PureComponent<BreadcrumbsProps> {
    public render() {
        if (this.props.crumbs.length <= 1) {
            return null;
        }
        
        return (
            <div id="breadcrumb-wrap" className="box-shadow">
                <Container>
                    <Breadcrumb>
                            {/* Link back to any previous steps of the breadcrumb. */}
                            {this.props.crumbs.map(({ name, path }, key) =>
                                key + 1 === this.props.crumbs.length ? (
                                    <BreadcrumbItem active>{name}</BreadcrumbItem>
                                ) : (
                                    <BreadcrumbItem>
                                        <NavLink tag={Link} className="p-0 d-inline" to={path}>{name}</NavLink>
                                    </BreadcrumbItem>
                                )
                            )}
                    </Breadcrumb>
                </Container>
            </div>
        )
    }
};

export default Breadcrumbs;
