import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Button } from 'reactstrap';
import { Card, CardBody, CardText, CardTitle } from 'reactstrap';
import { LoadCartridges } from '../../../utils/cartridgeUtils';
import Plot2Axis from '../../content-parts/Plot2Axis';
import PortStatusCard from '../../content-parts/PortStatusCard';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingData } from './data/ISolderingData';

type SolderingAssistantProps = {
    currentStep: number,
    lastRealTimeData: IRealTimeData,
    detectedSoldering: ISolderingData,

    selectedTemperature: number,
    selectedCartridge: string,
    selectedName: string,

    handleNextClick: () => void,
    handleSaveReferenceClick: () => void
}

class SolderingAssistantNewStep2 extends React.PureComponent<SolderingAssistantProps> {
    public render() {
        if (this.props.currentStep !== 2) {
            return null
        }

        const getPortStatus = () => {
            return <PortStatusCard portData={this.props.lastRealTimeData} />;
        }

        const getLastSoldering = () => {
            let data: IRealTimeData[];
            data = this.props.detectedSoldering.data.map(x => {
                return {
                    temperature: x.temperature / 9,
                    power: x.power / 10,
                    toolStatus: x.toolStatus,
                    isPortEnabled: x.isPortEnabled,
                    isCalibrating: x.isCalibrating,
                    isSoldering: x.isSoldering,
                    tenthsOfSecondSoldering: x.tenthsOfSecondSoldering
                }
            });

            let registeredPlot =
                <Plot2Axis
                    plotData={data}
                    counterTenthSecond={data.length}
                    interval={Math.floor(data.length / 5)}
                />;

            let saveButton = 
                this.props.detectedSoldering.solderingResult.tenthsOfSecondSoldering === 0 ?
                    "" :
                    <Button
                        color="danger"
                        size="sm"
                        className="m-2"
                        onClick={this.props.handleSaveReferenceClick}
                    >Save reference</Button>;

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Last soldering</CardTitle>
                        <CardText>Time: <strong>{this.props.detectedSoldering.solderingResult.tenthsOfSecondSoldering / 10} seconds</strong></CardText>
                        <CardText>Energy: <strong>{this.props.detectedSoldering.solderingResult.energy} ws</strong></CardText>
                        {registeredPlot}
                        {saveButton}
                    </CardBody>
                </Card>
            )
        }

        const getConfigurationCard = () => {
            let cartridges = LoadCartridges();
            let cartridge = cartridges.find(x => x.fullName === this.props.selectedCartridge);
            let cartridgeImagePath = cartridge === undefined ? "" : cartridge.img;

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Configuration</CardTitle>
                        <Row>
                            <Col xs="6">
                                <CardText>Selected temperature: {this.props.selectedTemperature / 9} C</CardText>
                                <CardText>Cartridge: {this.props.selectedCartridge}</CardText>
                                <CardText>Name: {this.props.selectedName}</CardText>
                            </Col>
                            <Col xs="6">
                                <img src={cartridgeImagePath} />
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getNextButton = () => {
            return (
                <Button
                    outline
                    color="danger"
                    size="sm"
                    className="m-2"
                    onClick={this.props.handleNextClick}
                >Previous</Button>)
        }
        
        return (
            <React.Fragment>
                <Row className="mb-1">
                    <h1>New soldering reference</h1>
                </Row>
                <Row className="mb-3">
                    {getPortStatus()}
                </Row>
                <Row className="mb-3">
                    <Col xs="8" className="pl-0">
                        {getLastSoldering()}
                    </Col>
                    <Col xs="4" className="pl-0 pr-0">
                        {getConfigurationCard()}
                    </Col>
                </Row>
                <Row>
                    {getNextButton()}
                </Row>
            </React.Fragment>
        );
    }
};

export default SolderingAssistantNewStep2;
