import * as React from 'react';
import { Link } from 'react-router-dom';
import { Col, Row, Button, CustomInput, Input } from 'reactstrap';
import { NavLink } from 'reactstrap';
import { Card, CardText, CardImg, CardBody, CardTitle } from 'reactstrap';
import * as TaskStore from '../../../store/Task';
import { GetTools, ToolImagePath } from '../../../utils/toolUtils';
import { TryParseInt } from '../../../utils/numberUtils';

type TaskNewProps = {
    newTask: (task: TaskStore.Task) => void,
}

interface IState {
    task: TaskStore.Task;
    toolImagePath: string;
}

class TaskNewView extends React.PureComponent<TaskNewProps, IState> {
    constructor(props: TaskNewProps) {
        super(props);

        let task = {
            id: 0,
            name: "",
            workConfig: {
                tool: 1,
                selectedTemp: 350 * 9,
                changeSelectedTemp: false
            },
            timesPerformed: 0
        }

        this.state = {
            task: task,
            toolImagePath: ToolImagePath(task.workConfig.tool)
        };
    }

    public render() {
        const getListTools = () => {
            let tools = GetTools();
            return (
                <Input type="select" onChange={(e) => handleToolChange(e.target.value)}>
                    {Object.entries(tools).map(([key, value]) => (
                        <option value={key}>{value}</option>
                    ))}
                </Input>
            )
        }

        const handleNameChange = (value: string) => {
            let newTask = this.state.task;
            newTask.name = value;
            
            this.setState({
                task: newTask
            });
        }

        const handleModifySelecTempChange = (value: boolean) => {
            let newTask = this.state.task;
            newTask.workConfig.changeSelectedTemp = value;

            this.setState({
                task: newTask
            });
        }

        const handleSelecTempChange = (value: string) => {
            let nValue = TryParseInt(value, null);

            if (nValue == null) {
                return
            }

            nValue *= 9;

            let newTask = this.state.task;
            newTask.workConfig.selectedTemp = nValue;

            this.setState({
                task: newTask
            });
        }

        const handleToolChange = (value: string) => {
            let nValue = TryParseInt(value, null);

            if (nValue == null) {
                return
            }

            let newTask = this.state.task;
            newTask.workConfig.tool = nValue;
            let newToolImagePath = ToolImagePath(newTask.workConfig.tool);

            this.setState({
                task: newTask,
                toolImagePath: newToolImagePath
            });
        }

        const handleConfirmClick = () => {
            this.props.newTask(this.state.task);
        };

        return (
            <React.Fragment>
                <Row className="mb-1">
                    <h1>New task</h1>
                </Row>
                <Row className="mb-3">
                    <Card>
                        <CardBody>
                            <CardTitle>Details</CardTitle>
                            <CardText>
                                <Row form>
                                    <Col md="auto" className="p-1">Name:</Col>
                                    <Col>
                                        <Input
                                            type="text"
                                            onChange={(e) => handleNameChange(e.target.value)} />
                                    </Col>
                                </Row>
                            </CardText>
                        </CardBody>
                    </Card>
                </Row>
                <Row>
                    <Card>
                        <CardBody>
                            <CardTitle>Configuration</CardTitle>
                            <Row>
                                <Col xs="8">

                                <CardText>
                                    <Row form>
                                        <Col md="auto" className="p-1">Tool:</Col>
                                        <Col>{getListTools()}</Col>
                                    </Row>
                                </CardText>
                                <CardText>
                                    <Row form>
                                        <Col md="auto" className="p-1">
                                            <CustomInput
                                                type="checkbox"
                                                className="danger"
                                                id="taskChangeSelectedTemp"
                                                label="Selected temperature"
                                                onChange={(e) => handleModifySelecTempChange(e.target.checked)}/>
                                        </Col>
                                        <Col>
                                            <Input
                                                type="text"
                                                pattern="[0-9]*"
                                                onChange={(e) => handleSelecTempChange(e.target.value)} />
                                        </Col>
                                    </Row>
                                </CardText>
                                </Col>
                                <Col xs="4">
                                    <CardImg src={this.state.toolImagePath} alt="No image available."/>
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Row>
                <Row>
                    <Button outline color="danger" size="sm" className="m-2" onClick={() => handleConfirmClick()}>
                        Create
                    </Button>

                    <Button color="danger" size="sm" className="m-2">
                        <NavLink
                            tag={Link}
                            className="text-white p-0"
                            to="/traceability/task">Cancel</NavLink>
                    </Button>
                </Row>
            </React.Fragment>
        );
    }
};

export default TaskNewView;
