import * as React from 'react';
import { connect } from 'react-redux';
import { ApplicationState } from '../../../store';
import * as AssistantStore from '../../../store/Assistant';
import AssistantService from '../../../services/assistant.service';
import SolderingAssistantView from './SolderingAssistantView';

type SolderingAssistantProps =
    AssistantStore.AssistantState &
    typeof AssistantStore.actionCreators;

interface IState {
    isLoading: boolean,
    nTotalReferences: number,
    pageSize: number,
    pageNumber: number
}

class SolderingAssistant extends React.PureComponent<SolderingAssistantProps, IState> {
    constructor(props: SolderingAssistantProps) {
        super(props);

        this.state = {
            isLoading: true,
            nTotalReferences: 0,
            pageSize: 25,
            pageNumber: 1
        };

        this.searchReferences(this.props, this.state.pageNumber);
    }

    searchReferences(props: SolderingAssistantProps, pageNumber: number) {
        props.deleteAllReferences();

        this.setState({
            isLoading: true,
            pageNumber: pageNumber
        })

        AssistantService
            .getReferences(pageNumber, this.state.pageSize)
            .then(references => {
                references.forEach((x: AssistantStore.Reference) =>
                    props.createReference(x)
                )

                this.setState({
                    isLoading: false
                })
            });

        AssistantService
            .countReferences()
            .then(value => {
                this.setState({
                    nTotalReferences: value
                })
            });
    }

    public render() {
        const handleDeleteReference = (id: number) => {
            AssistantService
                .deleteReference(id)
                .then(() =>
                    this.searchReferences(
                        this.props,
                        this.state.pageNumber)
                );
        }

        const handlePageClick = (pageNumber: number) => {
            this.searchReferences(
                this.props,
                pageNumber);
        }

        const handlePreviousPageClick = () => {
            this.searchReferences(
                this.props,
                Math.max(1, this.state.pageNumber - 1));
        }

        const handleNextPageClick = () => {
            this.searchReferences(
                this.props,
                Math.min(
                    Math.ceil(this.state.nTotalReferences / this.state.pageSize),
                    this.state.pageNumber + 1));
        }

        return (
            <SolderingAssistantView
                references={this.props.references}
                nTotalReferences={this.state.nTotalReferences}
                pageSize={this.state.pageSize}
                pageNumber={this.state.pageNumber}
                isLoading={this.state.isLoading}

                deleteReference={handleDeleteReference}

                handlePageClick={handlePageClick}
                handlePreviousPageClick={handlePreviousPageClick}
                handleNextPageClick={handleNextPageClick}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        references: state.assistant.references
    };
};

export default connect(
    mapStateToProps,
    AssistantStore.actionCreators
)(SolderingAssistant);
