import * as Assistant from './Assistant';
import * as Task from './Task';
import * as TaskHistory from './TaskHistory';
import * as Station from './Station';

// The top-level state object
export interface ApplicationState {
    assistant: Assistant.AssistantState;
    task: Task.TaskState;
    taskHistory: TaskHistory.TaskHistoryState;
    station: Station.StationState;
}

// Whenever an action is dispatched, Redux will update each top-level application state property using
// the reducer with the matching name. It's important that the names match exactly, and that the reducer
// acts on the corresponding ApplicationState property type.
export const reducers = {
    assistant: Assistant.reducer,
    task: Task.reducer,
    taskHistory: TaskHistory.reducer,
    station: Station.reducer
};

// This type can be used as a hint on action creators so that its 'dispatch' and 'getState' params are
// correctly typed to match your store.
export interface AppThunkAction<TAction> {
    (dispatch: (action: TAction) => void, getState: () => ApplicationState): void;
}
