import * as React from 'react';
import { Link } from 'react-router-dom';
import { Col, Row } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { NavLink } from 'reactstrap';

class Traceability extends React.PureComponent {
    public render() {
        const getTasks = () => {
            return (
                <NavLink tag={Link} to="/processes/optimization">
                    <Card className="box-shadow-v2">
                        <CardBody>
                            <CardText>
                                Access the list of registered jobs and create new tasks.
                            </CardText>
                            <CardTitle>Tasks</CardTitle>
                        </CardBody>
                    </Card>
                </NavLink>
            );
        }

        const getWorkProcess = () => {
            return (
                <NavLink tag={Link} to="/processes/assistant">
                    <Card className="box-shadow-v2">
                        <CardBody>
                            <CardText>
                                Register your work in the traceability system.
                            </CardText>
                            <CardTitle>Work process</CardTitle>
                        </CardBody>
                    </Card>
                </NavLink>
            );
        }

        const getWorkbenchs = () => {
            return (
                <NavLink tag={Link} to="/processes/assistant">
                    <Card className="box-shadow-v2">
                        <CardBody>
                            <CardText>
                                Manage workbenchs and connected stations.
                            </CardText>
                            <CardTitle>Workbenchs</CardTitle>
                        </CardBody>
                    </Card>
                </NavLink>
            );
        }

        return (
            <div className="list-cards">
                <Row xs={1} sm={2} md={4}>
                    <Col>{getTasks()}</Col>
                    <Col>{getWorkProcess()}</Col>
                    <Col>{getWorkbenchs()}</Col>
                </Row>
            </div>
        );
    }
};

export default Traceability;
