import * as React from 'react';
import { Alert } from 'reactstrap';
import { Col, Row, Input } from 'reactstrap';
import { Button } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import * as StationStore from '../../../store/Station';
import StationsPortList from '../../content-parts/StationsPortList';

type ProcessOptimizationProps = {
    currentStep: number,
    isLoading: boolean,
    isFormCorrect: boolean,

    stationControllers: StationStore.StationController[],
    selectedTemperature: number,
    
    handleStationClick: (station: StationStore.Station, port: number, tool: number) => void,
    handleSelecTempChange: (temperature: string) => void,
    handleNextClick: () => void
}

class ProcessOptimizationStep2 extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        if (this.props.currentStep !== 2) {
            return null
        }

        const getListOptionsStationControllers = () => {
            let filtered = this.props.stationControllers.map(sc => {
                let stations = sc.stations.map(s => {
                    let ports = s.ports.filter(p => p.tool === 1 || p.tool === 2);
                    s.ports = ports;
                    return s;
                });
                sc.stations = stations;
                return sc;
            });

            return (
                <StationsPortList
                    isLoading={this.props.isLoading}
                    stationControllers={filtered}
                    handleStationClick={this.props.handleStationClick}
                />
            );
        };

        const getConfigurationCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardTitle>Configuration</CardTitle>
                        <CardText>
                            <strong>Selected temperature:</strong>
                            <Input
                                type="select"
                                defaultValue={this.props.selectedTemperature / 9}
                                onChange={(e) => this.props.handleSelecTempChange(e.target.value)}
                            >
                                <option>425</option>
                                <option>400</option>
                                <option>375</option>
                                <option>350</option>
                                <option>325</option>
                                <option>300</option>
                            </Input>
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        const getNextButton = () => {
            if (!this.props.isFormCorrect) {
                return "";
            }

            return (
                <Button
                    outline
                    color="danger"
                    size="sm"
                    className="m-2"
                    onClick={this.props.handleNextClick}
                >Continue</Button>)
        }

        return (
            <React.Fragment>
                <Row className="mb-1">
                    <h1>Process optimization</h1>
                </Row>
                <Row className="mb-1">
                    <h4>Step 1 - Station selection</h4>
                </Row>
                <Row className="mb-1">
                    <Alert color="warning">
                        Select temperature to test with different cartridges
                    </Alert>
                </Row>
                <Row>
                    <Col xs="8">
                        <Row>
                            <Card>
                                <CardBody>
                                    <CardTitle>Stations available</CardTitle>
                                    <CardText>
                                        {getListOptionsStationControllers()}
                                    </CardText>
                                </CardBody>
                            </Card>
                        </Row>
                    </Col>
                    <Col xs="4" className="pr-0">
                        {getConfigurationCard()}
                    </Col>
                </Row>
                <Row>
                    {getNextButton()}
                </Row>
           </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep2;
